export const setSingleDaliLightDim = (value, id) => {
	const data = `{"DAPC\r\n":"${Math.floor(value)}\r\n","SHORT_ADDRESS\r\n":"${id}\r\n"}`;
	const buffer = Buffer.from(data);

	console.group();
	console.log(data);
	console.log(Buffer.from(data));
	console.groupEnd();

	return buffer;
}

