import { useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { useDispatch } from 'react-redux';
import { setLights, updateDim } from 'src/redux/actions';
import useLongPress from 'src/hooks/useLongPress';

// Components
import { RGBModal } from '../modals/Services';

// Icons
import { RGBSVG } from 'src/icons';
import { useSelector } from 'react-redux';
import { setGroupDaliLightDim, setRGBGroupDim, setRGBSingleDim, setSingleDaliLightDim } from 'src/libs/commands';

const useStyles = makeStyles(theme => createStyles({
	light: {
		width: '48px',
		height: '48px',
		backgroundColor: theme.palette.warning.main,
		boxShadow: `0px -4px 12px ${theme.palette.secondary.main}`,
		transition: theme.transitions.create(['all']),

		'& svg': {
			width: '24px',
			height: '24px',
		},

		[theme.breakpoints.down('md')]: {
			width: '36px',
			height: '36px',
	
			'& svg': {
				width: '20px',
				height: '20px',
			},
		},

		[theme.breakpoints.down('sm')]: {
			width: '28px',
			height: '28px',
	
			'& svg': {
				width: '18px',
				height: '18px',
			},
		},
	}
}));

const getStates = state => ({ lights: state.services.lights, mqtt: state.mqtt, project_id: state.user?.project_id });
const RGBBtn = ({ id, is_group, dim, serviceId, groupId, masterId, x, y, type, onRun }) => {
	const classes = useStyles();

	const { lights, mqtt, project_id } = useSelector(getStates);
	const dispatch = useDispatch();

	const [openModal, setOpenModal] = useState(false);

	const publishDT6 = (id, value) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		const dim = setSingleDaliLightDim(value, id);
		mqtt.publish(`${project_id}/${masterId}/Dali/In`, dim);
	}

	const publishDT8 = (id, value, hasRGB) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		const dim = is_group ? setGroupDaliLightDim(value[4], id) : setSingleDaliLightDim(value[4], id);
		mqtt.publish(`${project_id}/${masterId}/Dali/In`, dim);

		if (!hasRGB) return;
		const hex = `${value[0].toString(16)}${value[1].toString(16)}${value[2].toString(16)}`;
		const dec = parseInt(hex, 16);
		const rgbVal = is_group ? setRGBGroupDim(dec, id) : setRGBSingleDim(dec, id);
		mqtt.publish(`${project_id}/${masterId}/Dali/In`, rgbVal);
	}

	const onChangeBrightness = (brightness, rgb) => {
		const newVal = rgb ? [Math.floor(rgb.r * rgb.a), Math.floor(rgb.g * rgb.a), Math.floor(rgb.b * rgb.a), rgb.a, brightness] : [dim[0], dim[1], dim[2], dim[3], brightness];
		console.log(newVal);

		updateDim(id, newVal, type);
		onRun?.({ id, is_group, value: newVal, serviceId, groupId, masterId, x, y });

		const daliType = type.split('.')[1];
		if (daliType === 'dt6') {
			const [r, g, b] = newVal;

			publishDT6(serviceId, r);
			setTimeout(() => publishDT6(Number(serviceId) + 1, g), 110);
			setTimeout(() => publishDT6(Number(serviceId) + 2, b), 220);
		}
		else publishDT8(is_group ? groupId : serviceId, newVal, !!rgb);

		const newLights = lights.map(light => {
			if (light.id === id) return { ...light, value: newVal };

			return light;
		});
		dispatch(
			setLights(newLights)
		);
	}

	const longPressEvent = useLongPress(
		() => setOpenModal(true),
		() => onChangeBrightness(dim[4] < 127 ? 254 : 0, undefined),
		{
			delay: 1000
		}
	);

	return (
		<>
			<button
				{...longPressEvent}
				style={{ left: `${x}%`, top: `${y}%`, opacity: ((((dim[4] / 254) * 60) + 40) / 100) }}
				className={`${classes.light} btn text-light position-absolute rounded-circle p-0 m-0`}
				type="button"
			>
				<RGBSVG />
			</button>

			{openModal &&
				<RGBModal
					onClose={() => setOpenModal(false)}
					onChange={onChangeBrightness}
					open={openModal}
					initialBrightness={+dim[4]}
					initialRGB={{
						r: dim[0],
						g: dim[1],
						b: dim[2],
						a: dim[3]
					}}
				/>
			}
		</>
	);
};

export default RGBBtn;