import { useState, useRef, useEffect } from 'react';
import { makeStyles, createStyles, withStyles } from '@material-ui/styles';
import { Button, Slider } from '@material-ui/core';

// Components
import Modal from '../Modal';

const useStyles = makeStyles(theme => createStyles({
	modal: {
		maxWidth: '90%',
		width: '400px',
		borderRadius: '24px',
		minHeight: '400px',
		outline: 'none',
		backgroundColor: theme.palette.primary.main,
	},

	light: {
		'& .lamp': {
			borderRadius: '20%',
			width: '100px',
			display: 'inline-block',
			color: '#3c4657',
			transform: 'rotate(-5deg)',
			transformOrigin: 'top center',
			animation: 'light-lamp 3s forwards',
			animationIterationCount: 'infinite',


			'& .bulb': {
				filter: `drop-shadow(0 20px 20px ${theme.palette.warning.main})`,
				WebkitFilter: `drop-shadow(0 20px 20px ${theme.palette.warning.main})`,
				animation: 'light-bulb .3s .3s 5 cubic-bezier(.26,1.17,.89,-0.74) alternate',
			},
		},
	},

	rgb: {
		zIndex: 1,
		top: 'calc(100% + 4px)',

		'& .react-colorful__hue': {
			borderRadius: '0 0 4px 4px',
		},

		'& .react-colorful': {
			margin: '0 auto',
		}
	}
}));

const BrightnessSlider = withStyles({
	root: {
		height: '6px',

		'& .MuiSlider-rail': {
			height: '6px',
			backgroundColor: 'rgba(250, 250, 250, 0.4)',
			opacity: 1
		},

		'& .MuiSlider-track': {
			height: '6px',
			backgroundColor: 'rgb(250, 250, 250)'
		},

		'& .MuiSlider-thumb': {
			margin: '0 -2px 0 0',
			padding: 0,
			top: '50%',
			transform: 'translateY(-50%)',
			backgroundColor: 'rgb(250, 250, 250)'
		}
	}
})(props => <Slider {...props} />);

const CCTModal = ({ onClose, onChange, open, initialBrightness, initialCct }) => {
	const classes = useStyles();

	const rgbBox = useRef(null);
	const modalBox = useRef(null);

	const [brightness, setBrightness] = useState(initialBrightness);
	const [cw, setCw] = useState(+initialCct);

	const [openRgb, setOpenRgb] = useState(false);

	useEffect(() => {
		if (!modalBox.current) return;

		const handleOutsideClick = (e) => {
			const target = e.target;

			if (!rgbBox.current.contains(target)) {
				setOpenRgb(false);
			}
		};

		if (rgbBox.current && openRgb) {
			modalBox.current.onclick = (e) => handleOutsideClick(e);
		} else {
			modalBox.current.onclick = null;
		}
	}, [openRgb]);

	const confirm = () => {
		if (!onChange) return;

		onChange(brightness, +cw);
	}

	const cancel = () => {
		setBrightness(initialBrightness);

		if (onClose) onClose();
	}

	return (
		<Modal
			open={open}
			onClose={onClose}
		>
			<div ref={modalBox} className={`${classes.modal} d-flex flex-column justify-content-between position-absolute translate-middle start-50 top-50 px-5 pt-0 pb-4 m-0`}>
				<div className={`${classes.light} d-flex justify-content-center m-0 p-0`}>
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 60 130" className="lamp mx-auto text-center justify-content-center">
						<g>
							<circle style={{ filter: `drop-shadow(0 20px 20px #F2B90C)` }} fillOpacity={+brightness / 100} fill='#F2B90C' className="bulb" cx="30" cy="109.3" r="10.7" />
							<line fill="none" stroke="#D7D5AF" style={{ strokeWidth: .263, strokeLinecap: 'round', strokeMiterlimit: 10 }} x1="28.1" y1="108.1" x2="27.4" y2="113.4" />
							<line fill="none" stroke="#D7D5AF" style={{ strokeWidth: .263, strokeLinecap: 'round', strokeMiterlimit: 10 }} x1="32" y1="108.1" x2="32.6" y2="113.4" />
							<polyline fill="none" stroke="#D7D5AF" style={{ strokeWidth: .263, strokeLinecap: 'round', strokeMiterlimit: 10 }} points="27.8,113.5 28.3,112.8 28.8,113.5 29.6,112.8 30,113.5 30.7,112.9 31.2,113.5 31.8,112.8 32.3,113.5" />
						</g>
						<rect x="20.7" y="66.7" fill="currentColor" width="18.6" height="15.6" />
						<rect x="28.5" y="0" fill="currentColor" width="3" height="66.7" />
						<path fill="currentColor" d="M30,80.3c-16.6,0-30,13.4-30,30h60C60,93.8,46.6,80.3,30,80.3z" />
						<path fill="currentColor" d="M30,80.3c-16.6,0-30,13.4-30,30h60C60,93.8,46.6,80.3,30,80.3z" />
						<g className="switch">
							<line fill="none" stroke="currentColor" style={{ strokeWidth: .5, strokeMiterlimit: 10 }} x1="49" y1="100" x2="49" y2="118" />
							<circle fill="currentColor" stroke="currentColor" style={{ strokeWidth: .5, strokeMiterlimit: 10 }} cx="49" cy="120" r="1.6" />
						</g>
					</svg>
				</div>

				<div className="d-flex flex-column w-100 mt-4 mb-5 p-0">
					<div className="position-relative w-100 p-0 m-0">
						<BrightnessSlider
							value={brightness}
							step={1}
							min={0}
							max={254}
							valueLabelDisplay="auto"
							color="secondary"
							onChange={(_, value) => setBrightness(value)}
							valueLabelFormat={n => Math.abs(Math.floor(((n / 254) * 100)))}
						/>
					</div>

					<div
						className="w-100 p-2"
						style={{
							borderRadius: '4px',
							backgroundImage: 'linear-gradient(90deg, rgba(79, 195, 247), rgba(255, 183, 77))'
						}}
					>
						<input
							className='w-100'
							type='range'
							value={cw}
							onChange={e => setCw(e.target.value)}
							min='150'
							max='500'
						/>
					</div>
				</div>

				<div className="btn-group justify-content-end w-100 p-0 m-0">
					<Button variant="outlined" color="secondary" className="text-capitalize m-0" size="large" type="button" onClick={cancel}>Close</Button>
					<Button variant="contained" color="secondary" className="text-capitalize ms-2" size="large" type="button" onClick={confirm}>Set</Button>
				</div>
			</div>
		</Modal>
	);
}

export default CCTModal;