import axiosRequest from 'axios';
import Cookies from 'js-cookie';
import snackbar from 'src/components/common/Snackbar';

import { logout } from 'src/constants/routes';

// * Instance of axios
const axios = axiosRequest.create();
axios.CancelToken = axiosRequest.CancelToken;

export const unAuthorized = () => {
	Cookies.remove('client_id');
	window.location.href = logout;
}

// * Interceptors
axios.interceptors.request.use((config) => {
	const client_id = Cookies.get('client_id');

	if (client_id !== undefined) {
		config.headers.Authorization = `Bearer ${client_id}`;
	}
	return config;
}, (error) => Promise.reject(error));

axios.interceptors.response.use(
	(response) => {
		return response;
	},
	(error) => {
		if (error.response) {
			switch (error.response.status) {
				case 400: // Bad Request
					snackbar.error('Unfortunately, something went wrong');
					break;
				case 401: // Unauthorized
					unAuthorized();
					break;
				case 403: // Forbidden
					snackbar.error('Access Denied');
					break;
				case 404: // Not Found
					snackbar.error('Not Found', {
						preventDuplicate: true,
					});
					break;
				case 405: // Method Not Allowed
					snackbar.error('Method Not Allowed');
					break;
				case 408: // Request Timeout
					snackbar.error('No Internet, Checking the network cables, modem, and router', {
						preventDuplicate: true,
					});
					break;
				case 429: // Too Many Requests
					snackbar.error('Too Many Requests');
					break;
				case 500: // Internal Server Error
					snackbar.error('Internal Server Error', {
						preventDuplicate: true,
					});
					break;
				case 502: // Bad Gateway
					snackbar.error('Bad Gateway');
					break;
				case 503: // Service Unavailable
					snackbar.error('Service Unavailable');
					break;
				case 504: // Gateway Timeout
					snackbar.error('Gateway Timeout');
					break;
				case 511: // Network Authentication Required
					snackbar.error('Network Authentication Required');
					break;
				default:
					snackbar.error('Unfortunately, something went wrong');
					break;
			}
		} else if (error.request) {
			switch (error.message) {
				case 'Network Error':
					snackbar.error('No Internet, Checking the network cables, modem, and router');
					break;
				default:
					snackbar.error('Unfortunately, something went wrong');
					break;
			}
		} else {
			console.error('Error', error.message);
		}

		return Promise.reject(error);
	}
);

export default axios;