/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import { connect } from "react-redux";
import Cookies from 'js-cookie';
import { Route, Switch, Redirect } from "react-router-dom";

import { setAuthorized } from "src/redux/actions";

// Pages
import * as routes from "src/constants/routes";

// Pages
import Layout from "./layout";
import Auth from "./auth";
import LoadingPage from "src/pages/Loading";
import LogoutPage from "src/pages/Logout";

const App = ({ setAuthorized, authorized }) => {
	const [loading, setLoading] = useState(true);

	useEffect(() => {
		const client_id = Cookies.get('client_id');

		setAuthorized(!!client_id);
	}, []);

	useEffect(() => {
		setLoading(false);
	}, [authorized]);

	useEffect(() => {
		if (process.env.NODE_ENV === 'production') {
			window.addEventListener('contextmenu', (e) => {
				e.preventDefault();
				return false;
			});
		}
	}, []);

	if (loading) return (<LoadingPage><div className="spinner-border text-secondary" /></LoadingPage>);

	return (
		<Switch>
			{authorized
				? <Route exact sensitive path={[routes.dashboard, routes.sceneBuildings, routes.scenes, routes.moodBuildings, routes.moods, routes.light, routes.curtain, routes.relay, routes.thermostat, routes.jobs]} component={Layout} />
				: <Route exact sensitive path={routes.login} component={Auth} />
			}
			<Route exact sensitive path={routes.logout} component={LogoutPage} />
			<Route>
				<Redirect to={authorized ? routes.dashboard : routes.login} />
			</Route>
		</Switch>
	);
}

const mapStateToProps = ({ authorized }) => {
	return { authorized: authorized };
}

export default connect(mapStateToProps, {
	setAuthorized,
})(App);
