import { useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { useDispatch } from 'react-redux';
import { setThermostats, updateThermostat } from 'src/redux/actions';
import { useSelector } from 'react-redux';
import useLongPress from 'src/hooks/useLongPress';
import ThermostatModal from '../modals/Services/ThermostatModal';

// Icons
import { ThermostatLowSVG, ThermostatHighSVG, ThermostatQuartersSVG } from 'src/icons';
import { setThermostatLevel, setThermostatPower, setThermostatValue } from 'src/libs/commands';

const useStyles = makeStyles(theme => createStyles({
	th: {
		width: '48px',
		height: '48px',
		backgroundColor: theme.palette.warning.main,
		boxShadow: `0px -4px 12px ${theme.palette.secondary.main}`,
		transition: theme.transitions.create(['all']),

		'& svg': {
			width: '24px',
			height: '24px',
		},

		[theme.breakpoints.down('md')]: {
			width: '36px',
			height: '36px',

			'& svg': {
				width: '20px',
				height: '20px',
			},
		},

		[theme.breakpoints.down('sm')]: {
			width: '28px',
			height: '28px',

			'& svg': {
				width: '18px',
				height: '18px',
			},
		},
	},

	low: {
		opacity: '50%'
	},

	high: {
		opacity: '100%'
	},

	auto: {
		opacity: '75%'
	},
}));

const getStates = state => ({ thermostats: state.thermostats, mqtt: state.mqtt, project_id: state.user?.project_id });
const ThermostatBtn = ({ id, is_group, value, off, level, serviceId, groupId, masterId, building, x, y, onRun }) => {
	const classes = useStyles();

	const dispatch = useDispatch();

	const { thermostats, mqtt, project_id } = useSelector(getStates);

	const [openModal, setOpenModal] = useState(false);

	const longPressEvent = useLongPress(
		() => setOpenModal(true),
		() => onChangeLevel(),
		{
			delay: 1000
		}
	);

	/* SERVER REQUEST */
	const publish = (id, key, value) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		// value | level | off
		let command = "";
		if (key === 'off') command = setThermostatPower(id, value);
		if (key === 'value') command = setThermostatValue(id, value);
		if (key === 'level') command = setThermostatLevel(id, value);

		mqtt.publish(`${project_id}/Thermo/In`, command);
	}

	const onChangeLevel = () => {
		const nextLevels = { low: 'high', high: 'auto', auto: 'low' };
		const nextLevel = nextLevels[level] ?? 'low';

		/* UPDATE SERVER */
		updateThermostat(id, { level: nextLevel });
		onRun?.({ id, is_group, value, level: nextLevel, off, serviceId, groupId, masterId, x, y });

		/* Update UI */
		const newThermostats = [...thermostats].map(th => th.id === id ? { ...th, level: nextLevel } : th);
		dispatch(setThermostats(newThermostats));

		/* Update Device */
		publish(is_group ? groupId : serviceId, 'level', nextLevel);
	}

	const onChangeThermostat = (params) => {
		/* GET UPDATED VALUES */
		const updatedParams = {};
		if (off !== params.off) updatedParams.off = params.off;
		if (value !== params.value) updatedParams.value = params.value;
		if (level !== params.level) updatedParams.level = params.level;

		try {
			updateThermostat(id, updatedParams);
			onRun?.({ id, is_group, ...params, serviceId, groupId, masterId, x, y });

			/* Update UI */
			const newThermostats = [...thermostats].map(th => th.id === id ? { ...th, ...updatedParams } : th);
			dispatch(setThermostats(newThermostats));

			/* Update Device */
			let timeout = 0;
			const publishId = is_group ? groupId : serviceId;
			for (const key in updatedParams) {
				setTimeout(() => publish(publishId, key, updatedParams[key]), timeout);
				timeout += 500;
			}
		} catch (e) {
			//
		}
	};

	return (
		<>
			<button
				{...longPressEvent}
				style={{ left: `${x}%`, top: `${y}%` }}
				className={`${classes.th} ${classes[level]} btn text-light position-absolute rounded-circle p-0 m-0`}
				type="button"
			>
				{level === 'low'
					? <ThermostatLowSVG />
					: level === 'high'
						? <ThermostatHighSVG />
						: <ThermostatQuartersSVG />
				}
			</button>

			{openModal &&
				<ThermostatModal
					onClose={() => setOpenModal(false)}
					onChange={onChangeThermostat}
					open={openModal}
					building={building}
					defaultValue={value}
					defaultLevel={level}
					defaultIsOff={Boolean(off)}
				/>
			}
		</>
	);
};

export default ThermostatBtn;