import { createStyles, makeStyles } from '@material-ui/core';
import React from 'react';
import Modal from 'src/components/common/modals/Modal';

const useStyles = makeStyles(theme => createStyles({
	modal: {
		position: 'absolute',
		left: '50%',
		top: '50%',
		transform: 'translate(-50%, -50%)',
		WebkitTransform: 'translate(-50%, -50%)',
		MozTransform: 'translate(-50%, -50%)'
	},

	context: {
		position: 'relative',
		borderRadius: '4px',
		width: '200px',
		height: 'auto',
		backgroundColor: theme.palette.primary.main,
		border: `1px solid ${theme.palette.gray[800]}`,

		'& ul': {
			display: 'flex',
			flexDirection: 'column',
			width: '100%',
			padding: 0,
			margin: 0,

			'& li': {
				position: 'relative',
				borderBottom: `1px solid ${theme.palette.gray[800]}`,

				'&:last-child': {
					borderBottom: 'none'
				},

				'& button': {
					backgroundColor: 'transparent',
					border: 'none',
					display: 'flex',
					justifyContent: 'start',
					alignItems: 'center',
					height: '48px',
					width: '100%',
					padding: '0 16px',
					fontSize: '14px',
					color: theme.palette.gray[200]
				}
			}
		}
	}
}));

const ContextMenu = ({ onClose, list }) => {
	const classes = useStyles();

	return (
		<Modal
			open={true}
			onClose={onClose}
		>
			<div className={classes.modal}>
				<div className={classes.context}>
					<ul>
						{list.map((item, i) => (
							<li key={i}>
								<button type="button" onClick={item.onClick}>{item.title}</button>
							</li>
						))}
					</ul>
				</div>
			</div>
		</Modal>
	);
}

export default ContextMenu;
