import React from 'react';
import { useHistory } from 'react-router-dom';
import Buildings from 'src/components/layout/Container/Buildings/Buildings';

const SceneBuildings = () => {
	const history = useHistory();

	const redirect = (id) => history.push('/moods/' + id);

	return (
		<Buildings
			onClickBuilding={building => redirect(building.id)}
		/>
	);
}

export default SceneBuildings;
