import { useEffect, useMemo, useRef, useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import CircularSlider from '@fseehawer/react-circular-slider';

// Components
import Modal from '../Modal';
import { Button } from '@material-ui/core';

const useStyles = makeStyles(theme => createStyles({
	modal: {
		maxWidth: '90%',
		width: '400px',
		borderRadius: '24px',
		minHeight: '400px',
		outline: 'none',
		backgroundColor: theme.palette.primary.main,
	}
}));

const ThermostatModal = ({ onClose, onChange, open, building, defaultValue, defaultLevel, defaultIsOff }) => {
	const classes = useStyles();

	const modalBox = useRef(null);

	const timerRef = useRef(null);

	const [inputs, setInputs] = useState({
		value: Number(defaultValue),
		level: defaultLevel,
		off: defaultIsOff
	});

	const [temperature, setTemperature] = useState(Number(defaultValue));

	const FAN_LEVELS = useMemo(() => ([
		{ id: 'low', title: 'Low' },
		{ id: 'medium', title: 'Medium' },
		{ id: 'high', title: 'High' },
		{ id: 'auto', title: 'Auto' }
	]), []);

	const setFieldValue = (name, value) => {
		const params = { ...inputs, [name]: value };
		setInputs(params);

		if (inputs[name] !== value) onChange(params);
	};

	const rangeLabels = useMemo(() => {
		const temps = [];
		for (let i = 150; i <= 350; i = i + 5) {
			temps.push(i);
		}

		return temps;
	}, []);

	useEffect(() => {
		if (timerRef.current) clearTimeout(timerRef.current);

		timerRef.current = setTimeout(() => {
			setFieldValue('value', Number(temperature));
		}, 350);
	}, [temperature]);

	return (
		<Modal
			open={open}
			onClose={onClose}
		>
			<div ref={modalBox} className={`${classes.modal} position-absolute translate-middle start-50 top-50 px-5 pt-5 pb-4 m-0`}>
				<div className='d-flex flex-column h-100 mb-5' style={{ gap: '32px' }}>
					<div className='text-center'>
						<h3 style={{ fontWeight: 600, textTransform: 'uppercase', color: '#EEEDF0' }}>{building.name}</h3>
					</div>

					<div style={{ height: '144px' }} className='d-flex justify-content-center'>
						<CircularSlider
							width={144}
							label='Temperature'
							labelColor="#e0e0e0"
							knobColor="#e57373"
							progressColorFrom="#ef5350"
							progressColorTo="#ef5350"
							trackColor="#edcfce"
							trackSize={8}
							knobSize={48}
							data={rangeLabels}
							renderLabelValue={(
								<div className='position-absolute d-flex flex-column align-items-center justify-content-center w-100 h-100 top-0'>
									<div style={{ fontSize: '1rem' }}>Temperature</div>
									<div style={{ fontSize: '3rem', marginBottom: 'calc(1.5rem)' }} className='position-relative'>
										<span style={{ fontSize: '3rem' }}>{temperature / 10}C</span>
									</div>
								</div>
							)}
							dataIndex={rangeLabels.findIndex((n) => Number(n) === temperature) ?? 10}
							onChange={value => setTemperature(value)}
						/>
					</div>

					<div style={{ gap: '16px' }} className='d-flex justify-content-center align-items-center w-100'>
						<span style={{ fontWeight: 400, fontSize: '14px', color: '#bdbdbd' }}>Turn On/Off</span>

						<div
							onClick={() => setFieldValue('off', !inputs.off)}
							className='position-relative d-flex justify-content-between'
							style={{
								borderRadius: '8px',
								boxShadow: '0 0 8px rgba(250, 250, 250, 0.075) inset',
								height: '40px',
								width: '80px',
								cursor: 'pointer'
							}}
						>
							<button type='button' className='text-center h-100 border-0' style={{ outline: 'none', flex: 1, fontWeight: 500, fontSize: '14px', color: '#9e9e9e' }}>OFF</button>
							<button type='button' className='text-center h-100 border-0' style={{ outline: 'none', flex: 1, fontWeight: 500, fontSize: '14px', color: '#9e9e9e' }}>ON</button>

							<div
								className='position-absolute translate-middle-y top-50'
								style={{
									borderRadius: '8px',
									height: '36px',
									width: '36px',
									backgroundColor: '#F2B90C',
									boxShadow: '0 0 12px rgba(250, 250, 250, 0.1)',
									[inputs.off ? 'left' : 'right']: '2px'
								}}
							>
								<span className='d-flex justify-content-center align-items-center h-100' style={{ fontWeight: 500, fontSize: '14px', color: '#1B212C' }}>{inputs.off ? 'OFF' : 'ON'}</span>
							</div>
						</div>
					</div>

					<div style={{ gap: '24px' }} className='d-flex justify-content-center align-items-center w-100'>
						<span style={{ fontWeight: 400, fontSize: '12px', color: '#bdbdbd' }}>Fan</span>

						<ul style={{ gap: '16px' }} className='d-flex align-items-center m-0 p-0'>
							{FAN_LEVELS.map(fanLevel => (
								<li key={fanLevel.id}>
									<button
										style={{
											width: '64px',
											height: '44px',
											outline: 'none',
											color: fanLevel.id === inputs.level ? '#1B212C' : '#e0e0e0',
											fontSize: '12px',
											fontWeight: 500,
											cursor: 'pointer',
											boxShadow: '-4px -4px 8px rgba(250, 250, 250, 0.05), 4px 4px 8px rgba(200, 200, 200, 0.1)',
											borderRadius: '8px',
											border: '1px solid #212121',
											backgroundColor: fanLevel.id === inputs.level ? '#F2B90C' : 'transparent'
										}}
										onClick={() => setFieldValue('level', fanLevel.id)}
										className='d-flex justify-content-center align-items-center'
										type='button'
									>
										{fanLevel.title}
									</button>
								</li>
							))}
						</ul>
					</div>
				</div>

				<div className="btn-group justify-content-end w-100 p-0 m-0">
					<Button variant="outlined" color="secondary" className="text-capitalize m-0" size="large" type="button" onClick={onClose}>Close</Button>
				</div>
			</div>
		</Modal>
	);
};

export default ThermostatModal