import { useEffect, useState } from 'react';
import { createStyles, makeStyles } from '@material-ui/core';
import Modal from './Modal';

const useStyles = makeStyles(theme => createStyles({
	modal: {
		position: 'absolute',
		left: '50%',
		top: '50%',
		transform: 'translate(-50%, -50%)',
		WebkitTransform: 'translate(-50%, -50%)',
		MozTransform: 'translate(-50%, -50%)',

		'& form': {
			borderRadius: '4px',
			border: `1px solid ${theme.palette.gray[800]}`,
			width: '300px',
			display: 'flex',
			flexDirection: 'column',
			gap: '24px',
			padding: '16px',
			backgroundColor: theme.palette.primary.main,

			'& label': {
				display: 'flex',
				flexDirection: 'column',
				gap: '8px',

				'& > span': {
					color: 'rgb(250, 250, 250)',
					fontSize: '12px',
					display: 'flex',
					gap: '4px',
					alignItems: 'end',

					'& span': {
						color: 'rgb(200, 200, 200)',
						fontSize: '10px'
					}
				},

				'& input': {
					color: 'rgb(250, 250, 250)',
					padding: '0 12px',
					height: '48px',
					outline: 'none',
					fontSize: '12px',
					borderRadius: '4px',
					backgroundColor: 'transparent',
					border: `1px solid ${theme.palette.gray[800]}`
				}
			}
		}
	},

	btnGroup: {
		display: 'flex',
		gap: '16px',
		marginTop: '8px',

		'& button': {
			borderRadius: '4px',
			display: 'flex',
			justifyContent: 'center',
			alignItems: 'center',
			height: '40px',
			padding: '0 16px',
			outline: 'none',
			border: 'none',
			fontSize: '12px',
			color: 'rgb(224, 224, 224)',

			'&.save': {
				backgroundColor: theme.palette.success.main
			},

			'&.cancel': {
				border: '1px solid rgb(224, 224, 224)'
			}
		}
	}
}));

const AutoGenerateFormModal = ({ open, inputs, onClose, onAbort, onSubmit, closeAfterSubmit = true, cancelButtonName = 'Cancel', submitButtonName = 'Submit' }) => {
	const classes = useStyles();

	const inputsToValidObj = () => {
		let obj = {};

		inputs.forEach(item => {
			obj = {
				...obj,
				[item.name]: item.value ?? ""
			}
		});

		return obj;
	};

	const [inputValues, setInputValues] = useState(inputsToValidObj(inputs));

	const setFieldValue = (name, value) => {
		setInputValues({
			...inputValues,
			[name]: value
		})
	};

	const onSubmitForm = (e) => {
		e.preventDefault();

		onSubmit?.(e, { ...inputValues });
		if (closeAfterSubmit) onClose();
	};

	useEffect(() => {
		if (open) setInputValues(inputsToValidObj(inputs));
	}, [open]);

	return (
		<Modal
			open={open}
			onClose={onClose}
		>
			<div className={classes.modal}>
				<form onSubmit={onSubmitForm}>
					{inputs.map((input, i) => (
						<label key={i}>
							<span>
								{input.label}
								{!input.required && <span>(optional)</span>}
							</span>

							<input
								type={input?.type ?? 'text'}
								required={input.required}
								{...input}
								value={inputValues[input.name]}
								onChange={e => setFieldValue(input.name, e.target.value)}
							/>
						</label>
					))}

					<div className={classes.btnGroup}>
						<button type='submit' className='save'>{submitButtonName}</button>
						<button type='button' className='cancel' onClick={onAbort || onClose}>{cancelButtonName}</button>
					</div>
				</form>
			</div>
		</Modal>
	);
};

export default AutoGenerateFormModal;
