import { useEffect, useState } from "react";

const Clock = () => {
	const [time, setTime] = useState("");

	useEffect(() => {
		const calculateTime = () => {
			let date = new Date();
			let h = date.getHours(); // 0 - 23
			let m = date.getMinutes(); // 0 - 59
			let s = date.getSeconds(); // 0 - 59
			let session = "AM";

			if (h === 0) {
				h = 12;
			}

			if (h > 12) {
				h = h - 12;
				session = "PM";
			}

			h = (h < 10) ? "0" + h : h;
			m = (m < 10) ? "0" + m : m;
			s = (s < 10) ? "0" + s : s;

			setTime(h + ":" + m + ":" + s + " " + session);

			setTimeout(calculateTime, 1000);
		}

		calculateTime();
	}, []);

	useEffect(() => {
		setInterval(() => {
			setTime(time => time);
		}, 1000);
	}, []);

	return (
		<div className="d-flex flex-column text-end align-items-center justify-content-end h-100">
			<span style={{ fontFamily: 'Orbitron' }}>{time}</span>
		</div>
	);
}

export default Clock;
