import React from 'react';
import { NavLink } from 'react-router-dom';
import { makeStyles, createStyles } from '@material-ui/styles';

import { FOOTER_SERVICES } from 'src/constants/app';

const useStyles = makeStyles(theme => createStyles({
	footer: {
		backgroundColor: '#1B1D26',
		borderRadius: '24px 24px 0 0',
		height: '72px',

		'& ul>li': {
			'& a': {
				display: 'block',
				padding: '12px',
				color: theme.palette.gray[200],

				'& svg': {
					width: '24px',
					height: '24px',
				},

				'&.active': {
					color: theme.palette.secondary.main,
				}
			}
		}
	}
}));

const Footer = () => {
	const classes = useStyles();

	const isActiveHandler = (match, location, path) => {
		const regex = new RegExp(path, 'ig');
		return location.pathname.match(regex);
	}

	return (
		<footer style={{ marginTop: '64px' }} className="position-relative">
			<div className={`${classes.footer} position-fixed bottom-0 start-0 w-100 p-0 m-0`}>
				<ul className="list-group list-group-horizontal list-unstyled align-items-center justify-content-between w-100 h-100 py-2 px-5 m-0">
					{FOOTER_SERVICES.map((service, i) => (
						<li key={i + service.name}>
							<NavLink
								exact
								sensitive
								to={service.path}
								isActive={(match, location) => isActiveHandler(match, location, service.path)}
							>
								{service.icon}
							</NavLink>
						</li>
					))}
				</ul>
			</div>
		</footer>
	);
};

export default Footer;