import React, { useState } from 'react';
import { Helmet } from 'react-helmet';
import { makeStyles, createStyles } from '@material-ui/styles';
import { useDispatch, useSelector } from 'react-redux';

// Components
import Page from '../templates/Page';
import RuntimeModal from 'src/components/common/modals/Runtime';
import { addJob, deleteJob } from 'src/redux/actions';
import { zeroPad } from 'src/utils/helper';
import { TrashCanSVG } from 'src/icons';
import { useSnackbar } from 'notistack';
import clsx from 'clsx';

const useStyles = makeStyles(theme => createStyles({
	job: {
		width: '100%',
		maxWidth: '400px',
		minWidth: '290px',
		height: '96px',
		gap: '16px',
		padding: '24px',
		borderRadius: '8px',
		display: 'flex',
		backgroundColor: theme.palette.primary.dark,

		[theme.breakpoints.down('md')]: {
			maxWidth: '90%',
			minWidth: '90%',
		}
	},

	addNewJobBtn: {
		width: '100px',
		height: '40px',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		borderRadius: '48px',
		fontSize: '12px',
		fontWeight: 500,
		color: theme.palette.primary.main,
		backgroundColor: theme.palette.secondary.main,
		border: 0,
	},

	time: {
		fontSize: '18px',
		fontWeight: 500,
		color: 'rgb(250, 250, 250)'
	},

	list: {
		margin: 0,
		display: 'flex',
		justifyContent: 'end',
		alignItems: 'center',
		listStyle: 'none',
		gap: '8px',

		'& li': {
			fontSize: '14px',
			fontWeight: 500,
			color: 'rgb(250, 250, 250)',

			'&.active': {
				color: theme.palette.secondary.main
			}
		}
	},

	trash: {
		border: `1px solid ${theme.palette.secondary.main}`,
		borderRadius: '50%',
		width: '28px',
		height: '28px',
		backgroundColor: 'transparent',
		color: theme.palette.secondary.main,
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center'
	},

	secondaryText: {
		color: theme.palette.secondary.main
	}
}));

const WEEK_SUMMARY = {
	monday: 'M',
	tuesday: 'T',
	wednesday: 'W',
	thursday: 'T',
	friday: 'F',
	saturday: 'S',
	sunday: 'S',
};

const getJobs = (state) => state.jobs;
const Jobs = () => {
	const classes = useStyles();

	const jobs = useSelector(getJobs);

	const dispatch = useDispatch();

	const { enqueueSnackbar } = useSnackbar();

	const [addJobModalIsOpen, setAddJobModalIsOpen] = useState(false);

	const onAddJob = (_e, values) => {
		const params = {
			time: `${zeroPad(+values.h)}:${zeroPad(+values.m)}`
		};

		if (values.e.length) params.every = values.e;
		else {
			const d = values.d;
			params.date = d.getFullYear() + '-' + d.getHours() + '-' + d.getMinutes();
		}
		dispatch(
			addJob(values.t, values.i.value, params)
		);
	};

	const onDeleteJob = (job) => {
		dispatch(deleteJob(job.id));
		enqueueSnackbar('Job deleted successfully.', {
			variant: 'success'
		})
	};

	const renderDate = (job) => {
		if (job.date && !job.every) {
			const d = new Date(job.date);
			return (<span className={clsx(classes.secondaryText, 'font-size-14 font-weight-500')}>{d.toDateString()}</span>);
		}

		if (job.every.length === Object.keys(WEEK_SUMMARY).length) return (<span className={clsx(classes.secondaryText, 'font-size-14 font-weight-500')}>Every Day</span>);

		return (
			<ul className={classes.list}>
				{Object.keys(WEEK_SUMMARY).map(day => (
					<li key={day} className={job.every.includes(day) ? 'active' : undefined}>{WEEK_SUMMARY[day]}</li>
				))}
			</ul>
		);
	};

	const renderJobs = () => {
		if (jobs.length === 0) return (
			<div style={{ height: 'calc(100vh - 136px)' }} className='d-flex align-items-center justify-content-center w-100'>
				<div style={{ gap: '16px' }} className='d-flex flex-column align-items-center'>
					<span>No job found, You want to add a new job?</span>
					<button type='button' className={classes.addNewJobBtn} onClick={() => setAddJobModalIsOpen(true)}>+ Add job</button>
				</div>
			</div>
		);

		return (
			<div className='d-flex flex-column align-items-center' style={{ marginTop: '32px', gap: '32px' }}>
				<div className='d-flex justify-content-center'>
					<button type='button' className={classes.addNewJobBtn} onClick={() => setAddJobModalIsOpen(true)}>+ Add job</button>
				</div>

				<div className='row align-items-center justify-content-center w-100' style={{ gap: '24px' }}>
					{jobs.map((job, i) => (
						<div key={i} className={classes.job}>
							<div className='flex-fill d-flex justify-content-first align-items-center h-100'>
								<span className={classes.time}>{job.time}</span>
							</div>

							<div style={{ gap: '16px' }} className='flex-fill d-flex justify-content-end align-items-center h-100'>
								{renderDate(job)}

								<button className={classes.trash} type='button' onClick={() => onDeleteJob(job)}>
									<TrashCanSVG width="14px" height="14px" />
								</button>
							</div>
						</div>
					))}
				</div>
			</div>
		);
	};

	return (
		<Page uniqueKey="jobs">
			<Helmet>
				<title>Automatic Jobs</title>
			</Helmet>

			<div className="row justify-content-center mx-auto my-0">
				{renderJobs()}
			</div>

			<RuntimeModal
				open={addJobModalIsOpen}
				onClose={() => setAddJobModalIsOpen(false)}
				onSubmit={onAddJob}
			/>
		</Page>
	);
};

export default Jobs;
