import SelectInput from 'react-select';
import styles from './Input.module.scss';

const Select = ({ options, label, error, touched, ...props }) => {
	return (
		<div className={styles.inputGroup}>
			<label>
				{label &&
					<div className={styles.label}>
						<span>{label}</span>
					</div>
				}
				<SelectInput
					options={options}
					{...props}
					styles={{
						container: (provided) => ({
							...provided,
							width: '100%'
						}),

						control: (provided) => ({
							...provided,
							minHeight: '40px',
							backgroundColor: 'transparent',
							borderColor: (touched && error) ? 'rgb(211, 47, 47)' : 'rgb(66, 66, 66)'
						}),

						input: (provided) => ({
							...provided,
							fontSize: '12px',
						}),

						placeholder: (provided) => ({
							...provided,
							color: 'rgb(224, 224, 224)',
							fontSize: '12px'
						}),

						indicatorSeparator: (provided) => ({
							...provided,
							backgroundColor: 'rgb(27, 33, 44)',
						}),

						menu: (provided) => ({
							...provided,
							border: '1px solid rgb(27, 33, 44)',
							backgroundColor: 'rgb(27, 33, 44)',
							padding: 0,
							fontSize: '12px'
						}),

						option: (provided, state) => ({
							...provided,
							color: state.isSelected ? 'rgb(242, 185, 12)' : 'rgb(224, 224, 224)',
							backgroundColor: 'transparent'
						}),

						singleValue: (provided) => ({
							...provided,
							color: 'rgb(250, 250, 250)',
							fontSize: '12px',
						}),

						
					}}
				/>

				{(touched && error) &&
					<div className={styles.errorMsg}>{error}</div>
				}
			</label>
		</div>
	);
}

export default Select;
