import { useDispatch, useSelector } from 'react-redux';
import { makeStyles, createStyles } from '@material-ui/styles';
import { setCurtains, updateDim } from 'src/redux/actions';
import useLongPress from 'src/hooks/useLongPress';
import { setCurtainValue } from 'src/libs/commands';

// Gif
const CurtainOpenGIF = process.env.PUBLIC_URL + '/images/curtain-open.gif';
const CurtainCloseGIF = process.env.PUBLIC_URL + '/images/curtain-close.gif';

const useStyles = makeStyles(theme => createStyles({
	curtain: {
		width: '48px',
		height: '48px',
		boxShadow: theme.shadows[3],

		[theme.breakpoints.down('md')]: {
			width: '36px',
			height: '36px',
		},

		[theme.breakpoints.down('sm')]: {
			width: '28px',
			height: '28px',
		},
	}
}));

const getProjectId = state => ({ project_id: state.user?.project_id, mqtt: state.mqtt, curtains: state.services.curtains });
const CurtainBtn = ({ is_group, serviceId, dim, id, x, y, onRun }) => {
	const classes = useStyles();

	const dispatch = useDispatch();

	const { project_id, mqtt, curtains } = useSelector(getProjectId);

	const publish = (id, value) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		mqtt.publish(`${project_id}/Curtain/In`, setCurtainValue(id, value));
	}

	const onChangeDim = (value) => {
		const formattedValue = (!!value) ? 100 : 0;

		updateDim(id, Boolean(formattedValue), 'curtain');
		onRun?.({ id, is_group, value: formattedValue, serviceId, x, y });

		const newCurtains = curtains.map(curtain => {
			if (curtain.id === id) return { ...curtain, value: formattedValue };

			return curtain;
		});
		dispatch(setCurtains(newCurtains));

		publish(serviceId, (!!value) ? 'open' : 'close');
	}

	const longPressEvent = useLongPress(
		() => publish(serviceId, 'stop'),
		() => onChangeDim(!(!!dim)),
		{
			delay: 750
		}
	);

	return (
		<button
			{...longPressEvent}
			style={{ left: `${x}%`, top: `${y}%` }}
			className={`${classes.curtain} btn text-light position-absolute overflow-hidden rounded-circle p-0 m-0`}
			type="button"
		>
			<img src={(!!dim) ? CurtainOpenGIF : CurtainCloseGIF} alt={(!!dim) ? 'open' : 'close'} className="img-fluid rounded-circle" />
		</button>
	);
};

export default CurtainBtn;