import { useMemo, useState } from 'react';

// Components
import Page from './templates/Page';
import Buildings from 'src/components/layout/Container/Buildings/Buildings';
import BuildingFacades from 'src/components/common/modals/BuildingFacades';
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';
import ThermostatBtn from 'src/components/common/buttons/ThermostatBtn';
import { Fragment } from 'react';

const getThermostats = (state) => state.thermostats;
const Thermostat = () => {
	const thermostats = useSelector(getThermostats);

	const [openBuildingFacadesModal, setOpenBuildingFacadesModal] = useState(false);

	const [building, setBuilding] = useState(null);

	const handleClickBuilding = (building) => {
		setBuilding(building);
		setOpenBuildingFacadesModal(true);
	}

	const buildingThermostats = useMemo(() => {
		if (!building || !Array.isArray(thermostats)) return [];

		return thermostats.filter(s => s.building_id === building.id)
	}, [building, thermostats]);

	return (
		<Page uniqueKey="light">
			<Helmet>
				<title>Thermostat | Service</title>
			</Helmet>

			<Buildings onClickBuilding={handleClickBuilding} />

			{(openBuildingFacadesModal && building) &&
				<BuildingFacades
					building={building}
					open={openBuildingFacadesModal}
					onClose={() => setOpenBuildingFacadesModal(false)}
				>
					{buildingThermostats.map((th) => (
						<Fragment key={th.id}>
							<ThermostatBtn
								id={th.id}
								x={th.location[0]}
								y={th.location[1]}
								value={Number(th.to_value)}
								off={Boolean(th.off)}
								level={th.level}
								serviceId={th.service_id}
								is_group={th.group_id !== null}
								groupId={th.group_id}
								building={building}
							/>
						</Fragment>
					))}
				</BuildingFacades>
			}
		</Page>
	);
}

export default Thermostat;