const WEEK_SUMMARY = {
	monday: 'Mon',
	tuesday: 'Tue',
	wednesday: 'Wed',
	thursday: 'Thu',
	friday: 'Fri',
	saturday: 'Sat',
	sunday: 'Sun',
};

const WEEK = [
	{ title: 'monday', summary: WEEK_SUMMARY.monday, letter: 'M' },
	{ title: 'tuesday', summary: WEEK_SUMMARY.tuesday, letter: 'T' },
	{ title: 'wednesday', summary: WEEK_SUMMARY.wednesday, letter: 'W' },
	{ title: 'thursday', summary: WEEK_SUMMARY.thursday, letter: 'T', className: 'thu' },
	{ title: 'friday', summary: WEEK_SUMMARY.friday, letter: 'F', className: 'fri' },
	{ title: 'saturday', summary: WEEK_SUMMARY.saturday, letter: 'S' },
	{ title: 'sunday', summary: WEEK_SUMMARY.sunday, letter: 'S' }
];

const getMoodsAndScenes = (state) => ({ moods: state.moods, scenes: state.scenes });
const RuntimeModal = ({ open, onClose, onAbort, onSubmit, closeAfterSubmit = true, cancelButtonName = 'Cancel', submitButtonName = 'Submit' }) => {
	const classes = useStyles();

	const [inputs, setInputs] = useState({
		h: 6, // Hour
		m: 0, // Min
		d: new Date(), // Date
		e: [], // Every
		t: 'scene', // Type
		i: null // ID | Index
	});

	const [selectError, setSelectError] = useState(null);

	const { moods, scenes } = useSelector(getMoodsAndScenes);

	const setFieldValue = (name, value) => {
		setInputs({
			...inputs,
			[name]: value
		})
	};

	const onSubmitForm = (e) => {
		e.preventDefault();

		if (!inputs.i) {
			return setSelectError("Please choose a " + inputs.t);
		}

		onSubmit?.(e, { ...inputs });
		if (closeAfterSubmit) onClose();
	};

	const addWeek = (day) => {
		const dayId = inputs.e.indexOf(day);
		const newDays = dayId === -1 ? [...inputs.e, day] : inputs.e.filter(d => d !== day);

		setFieldValue('e', newDays);
	};

	const getSelectOptions = useMemo(() => {
		try {
			const data = inputs.t === 'scene' ? scenes : moods;

			return data.map(item => ({ value: item.id, label: item.name }));
		} catch (e) {
			return [];
		}
	}, [inputs.t, scenes, moods]);

	const renderSelectDay = () => {
		if (inputs.e.length) {
			if (inputs.e.length < WEEK.length) {
				let text = "Every ";
				inputs.e.forEach(day => {
					text += WEEK_SUMMARY[day] + ', ';
				});

				return text.substr(0, text.length - 3);
			}

			if (inputs.e.length === WEEK.length) return 'Every day';
		}

		if (!inputs.d) return 'Tomorrow';

		const d = new Date(inputs.d)
		return d.toDateString();
	};

	const CustomDatepicker = forwardRef(({ onClick }, ref) => (
		<button type='button' onClick={onClick} ref={ref}>
			<CalendarDaySVG width='16px' height='16px' />
		</button>
	));

	useEffect(() => {
		if (open) setInputs({ h: 6, m: 0, d: new Date(), e: [], t: 'scene', i: null });
	}, [open]);

	return (
		<Modal
			open={open}
			onClose={onClose}
		>
			<div className={classes.modal}>
				<form onSubmit={onSubmitForm}>
					<div className={classes.tabs}>
						<button className={inputs.t === 'scene' ? 'active' : undefined} type='button' onClick={() => setFieldValue('t', 'scene')}>Scene</button>
						<button className={inputs.t === 'mood' ? 'active' : undefined} type='button' onClick={() => setFieldValue('t', 'mood')}>Mood</button>
					</div>

					<div className={classes.flex}>
						<div className='input-group'>
							<label>
								<span>Hour</span>
								<input
									style={{ textAlign: 'center' }}
									type='number'
									value={String(zeroPad(inputs.h))}
									onChange={e => {
										const value = e.target.valueAsNumber;
										if (value > 23) return;

										setFieldValue('h', (isNaN(value) || value < 0) ? 0 : value);
									}}
								/>
							</label>
						</div>

						<div className='input-group'>
							<label>
								<span>Minute</span>
								<input
									style={{ textAlign: 'center' }}
									type='number'
									value={String(zeroPad(inputs.m))}
									onChange={e => {
										const value = e.target.valueAsNumber;
										if (value > 59) return;

										setFieldValue('m', (isNaN(value) || value < 0) ? 0 : value);
									}}
								/>
							</label>
						</div>
					</div>

					<div className={classes.date}>
						<div className={classes.datepicker}>
							<div className='selected-day'>
								<span>{renderSelectDay()}</span>
							</div>
							<div className='picker'>
								<DatePicker
									showPopperArrow={false}
									selected={inputs.d}
									onChange={d => setFieldValue('d', d)}
									customInput={<CustomDatepicker />}
								/>
							</div>
						</div>

						<div className={classes.week}>
							{WEEK.map((day, i) => (
								<button
									key={i}
									title={day.title}
									type='button'
									className={clsx(day.className, {
										active: inputs.e.includes(day.title)
									})}
									onClick={() => addWeek(day.title)}
								>
									{day.letter}
								</button>
							))}
						</div>
					</div>

					<Select
						options={getSelectOptions}
						label={inputs.t === 'scene' ? 'Scene' : 'Mood'}
						onChange={(val) => {
							if (val && selectError) setSelectError(null);
							setFieldValue('i', val);
						}}
						touched={!!selectError}
						error={selectError}
					/>

					<div className={classes.btnGroup}>
						<button type='submit' className='save'>{submitButtonName}</button>
						<button type='button' className='cancel' onClick={onAbort || onClose}>{cancelButtonName}</button>
					</div>
				</form>
			</div>
		</Modal>
	);
}

export default RuntimeModal;
