import React from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { withStyles } from '@material-ui/core/styles';
import { Switch } from '@material-ui/core';
import { updateDim } from 'src/redux/actions';
import { useDispatch } from 'react-redux';

// Axios
import { useSelector } from 'react-redux';

const useStyles = makeStyles(theme => createStyles({
	power: {
		maxWidth: '75px',
		maxHeight: '75px',
	}
}));

const IOSSwitch = withStyles((theme) => ({
	root: {
		width: 42,
		height: 26,
		padding: 0,
		margin: theme.spacing(1),
	},
	switchBase: {
		padding: 1,
		'&$checked': {
			transform: 'translateX(16px)',
			color: theme.palette.common.white,
			'& + $track': {
				backgroundColor: theme.palette.secondary.main,
				opacity: 1,
				border: 'none',
			},
		},
		'&$focusVisible $thumb': {
			color: '#52d869',
			border: '6px solid #fff',
		},
	},
	thumb: {
		width: 24,
		height: 24,
	},
	track: {
		borderRadius: 26 / 2,
		border: `1px solid ${theme.palette.grey[400]}`,
		backgroundColor: theme.palette.grey[50],
		opacity: 1,
		transition: theme.transitions.create(['background-color', 'border']),
	},
	checked: {},
	focusVisible: {},
}))(({ classes, ...props }) => {
	return (
		<Switch
			focusVisibleClassName={classes.focusVisible}
			disableRipple
			classes={{
				root: classes.root,
				switchBase: classes.switchBase,
				thumb: classes.thumb,
				track: classes.track,
				checked: classes.checked,
			}}
			{...props}
		/>
	);
});

const getProjectId = state => ({ project_id: state.user?.project_id });
const RelayBtn = ({ mqtt, relays, setRelays, masterId, is_group, serviceId, dim, id, x, y, onRun }) => {
	const classes = useStyles();

	const dispatch = useDispatch();

	const project_id = useSelector(getProjectId);

	const publish = (id, value) => {
		const data = `{"command":"${value}","id":"${id}"}`;

		mqtt.publish(data, `${project_id}/${masterId}/Relay/In`);
	}

	const onChangeDim = (value) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		const formattedValue = (!!value) ? 0 : 100;

		updateDim(id, Boolean(formattedValue), 'relay');
		onRun?.({ id, is_group, value: formattedValue, serviceId, masterId, x, y });

		if (Array.isArray(serviceId) && is_group) publish(serviceId, (!!value) ? 'on' : 'off');
		else {
			serviceId.forEach(id => {
				publish(id, value);
			});
		}

		const newRelays = relays.map(light => {
			if (light.id === id) return { ...light, value: formattedValue };

			return light;
		});
		dispatch(setRelays(newRelays));
	}

	return (
		<button
			style={{ left: `${x}px`, top: `${y}px` }}
			className={`${classes.power} btn text-light position-absolute overflow-hidden p-0 m-0`}
		>
			<IOSSwitch
				checked={!!dim}
				onChange={(e) => onChangeDim(e.target.checked)}
			/>
		</button>
	);
};

export default RelayBtn;