import { makeStyles, createStyles } from '@material-ui/styles';
import { alpha } from '@material-ui/core';
import { connect } from 'react-redux';
import { useSelector } from 'react-redux';
import { useState } from 'react';
import useLongPress from 'src/hooks/useLongPress';
import { createPortal } from 'react-dom';
import ContextMenu from '../../../common/ContextMenu';
import AutoGenerateFormModal from 'src/components/common/modals/Form';
import axios from 'src/api/axios';
import * as api from 'src/api/routes';
import { useSnackbar } from 'notistack';

const useStyles = makeStyles(theme => createStyles({
	scene: {
		width: '256px',
		height: '170px',
		borderRadius: '4px',
		boxShadow: `0px 3px 3px -2px ${alpha(theme.palette.primary.main, .2)},0px 3px 4px 0px ${alpha(theme.palette.primary.main, .14)},0px 1px 8px 0px ${alpha(theme.palette.primary.main, .12)}`,
		margin: '16px',
		transition: theme.transitions.create(['all', {
			duration: '250ms',
		}]),

		'& .bg-image': {
			background: `${theme.palette.gray[800]} no-repeat scroll center`,
			backgroundSize: 'cover',
			filter: 'brightness(.6)',
			WebkitFilter: 'brightness(.6)',
		},
	},

	wrapper: {
		position: 'absolute',
		width: '100%',
		height: '100%',
		backgroundColor: 'rgba(0, 0, 0, 0.5)',
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',

		'& span': {
			fontSize: '14px',
			fontWeight: 500
		}
	},

	btn: {
		background: 'rgba(0, 0, 0, .25) linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.85) 90%, rgba(0, 0, 0, 0.9) 100%);',
		padding: '0 0 16px 16px',
		position: 'relative',
		fontSize: '14px',
		color: 'rgb(224, 224, 224)',
		display: 'flex',
		justifyContent: 'flex-start',
		alignItems: 'flex-end',

		'&:hover': {
			color: 'rgb(255, 255, 255)'
		}
	}
}));

const getProjectId = state => state.user?.project_id;
const Scene = ({ id, mqtt, name, image, masterId, sceneId }) => {
	const classes = useStyles();

	const { enqueueSnackbar } = useSnackbar();

	const [run, setRun] = useState(false);

	const [contextMenuIsOpen, setContextMenuIsOpen] = useState(false);

	const [runtimeModalIsOpen, setRuntimeModalIsOpen] = useState(false);

	const project_id = useSelector(getProjectId);

	const publish = (mastersId, index = 0) => {
		if (!mqtt) return;
		const currentMasterId = mastersId[index];

		console.log(`${project_id}/${currentMasterId}/Dali/In`);

		const data = `{"CMD\r\n":"GO_TO_SCENE_${sceneId}\r\n","BROADCAST\r\n":"ALL\r\n"}`;
		mqtt.publish(`${project_id}/${currentMasterId}/Dali/In`, Buffer.from(data));

		/* RUN */
		const nextIndex = index + 1;
		if (nextIndex < mastersId.length) setTimeout(() => publish(mastersId, nextIndex), 350);
		else setTimeout(() => setRun(false), 1000);
	}

	const overallPublish = () => {
		if (!mqtt) return;
		setRun(true);

		const mastersId = String(masterId).split(',');
		publish(mastersId, 0);
	}

	const toggleContextMenu = () => setContextMenuIsOpen(!contextMenuIsOpen);

	const onChangeRuntime = async (_e, values) => {
		try {
			const { data } = await axios.patch(api.scenes.update(id), {
				runtime: values.runtime
			});

			if (!data.succeeded) throw new Error(data.errors[0]);

			enqueueSnackbar("Mood updated successfully.", {
				variant: 'success'
			});
		} catch (e) {
			console.log(e);
		}
	};

	const longPressEvent = useLongPress(
		toggleContextMenu,
		overallPublish,
		{
			delay: 750
		}
	);

	return (
		<div className={`${classes.scene} position-relative overflow-hidden p-0`}>
			<div style={{ backgroundImage: `url('${image}')` }} className="bg-image position-absolute start-0 top-0 w-100 h-100 p-0 m-0" />

			{run
				?
				<div className={classes.wrapper}>
					<span>Scene is ready</span>
				</div>
				:
				<button
					{...longPressEvent}
					className={classes.btn + " btn w-100 h-100"}
				>
					{name}
				</button>
			}

			{contextMenuIsOpen &&
				createPortal(<ContextMenu
					list={[
						/* {
							title: 'Runtime', onClick: () => {
								setContextMenuIsOpen(false);
								setRuntimeModalIsOpen(true);
							}
						}, */
						{ title: 'Run', onClick: overallPublish },
						{ title: 'Cancel', onClick: () => setContextMenuIsOpen(false) }
					]}
				/>, document.body)}

			<AutoGenerateFormModal
				open={runtimeModalIsOpen}
				onClose={() => setRuntimeModalIsOpen(false)}
				onSubmit={onChangeRuntime}
				inputs={[
					{
						required: true,
						type: 'datetime-local',
						name: 'runtime',
						label: 'Runtime',
						placeholder: 'Enter date and time'
					}
				]}
			/>
		</div>
	);
};

const mapStateToProps = ({ mqtt }) => {
	return { mqtt };
};

export default connect(mapStateToProps)(Scene);
