import React from 'react';
import { connect } from 'react-redux';

// Components
import Page from '../templates/Page';
import Scene from 'src/components/layout/Container/Scenes/Scene';
import { useParams } from 'react-router-dom';
import { useMemo } from 'react';
import SceneBuildings from './Buildings';

const Scenes = ({ scenes }) => {
	const params = useParams();

	const filteredScenes = useMemo(() => scenes.filter(s => +s.building_id === +params.buildingId), [scenes, params.buildingId]);

	return (
		<Page uniqueKey="scene">
			<div className="row justify-content-center mx-auto my-0">
				{filteredScenes.map((scene, i) => (
					<Scene
						key={i}
						id={scene.id}
						name={scene.name}
						image={scene.image}
						masterId={scene.master_id}
						sceneId={scene.scene_id}
					/>
				))}
			</div>
		</Page>
	);
};

const mapStateToProps = ({ scenes }) => {
	return { scenes: scenes };
}

export { SceneBuildings };
export default connect(mapStateToProps)(Scenes);