import { useState } from 'react';
import { connect } from 'react-redux';
import { makeStyles, createStyles } from '@material-ui/styles';
import { alpha } from '@material-ui/core';
import { Helmet } from 'react-helmet';
import { useSnackbar } from 'notistack';


import Cookies from 'js-cookie';
import { login as loginAPI } from 'src/api/routes';
import { setAuthorized, setUser } from 'src/redux/actions';

// Components
import Auth from './templates/Auth';
import { CircularProgress } from '@material-ui/core';
import axios from 'src/api/axios';

const useStyles = makeStyles(theme => createStyles({
	form: {
		top: '200px',
		width: '400px',
		maxWidth: '95%',
		backgroundColor: '#1f2632',
		boxShadow: theme.shadows[4],

		'& .input-group': {
			height: '40px',
			border: '1px solid #1f2632',
			boxShadow: theme.shadows[1],
			color: theme.palette.gray[600],

			'& input': {
				fontSize: '13px',
				outline: 'none',
				transition: theme.transitions.create(['all'], {
					duration: '300ms',
				}),

				'&:focus': {
					color: theme.palette.gray[300],
					transform: 'scale(1.05)',
				},

				'&::placeholder': {
					color: 'inherit',
				},
			},

			'& .input-group-text, & input': {
				backgroundColor: theme.palette.primary.main,
				color: 'inherit',
			},

			'&.is-invalid': {
				borderColor: theme.palette.error.main,
				boxShadow: `0px 3px 5px -1px ${alpha(theme.palette.error.main, .2)},0px 5px 8px 0px ${alpha(theme.palette.error.main, .14)},0px 1px 14px 0px ${alpha(theme.palette.error.main, .12)}`,
			}
		},

		'& button[type="submit"]': {
			backgroundColor: theme.palette.info.main,
			color: theme.palette.gray[50],
			padding: '9px 12px',
			transition: theme.transitions.create(['all'], {
				duration: '250ms',
			}),

			'&:hover': {
				backgroundColor: '#3B4654',
				color: '#8F9396',
			},

			'&:disabled': {
				opacity: 0.6,
				backgroundColor: '#3B4654',
				color: '#8F9396',
				cursor: 'not-allowed',
			}
		}
	}
}));

const Login = ({ setAuthorized, setUser }) => {
	const { enqueueSnackbar, closeSnackbar } = useSnackbar();

	const classes = useStyles();

	const [errors, setErrors] = useState({});
	const [submitting, setSubmitting] = useState(false);

	const [email, setEmail] = useState("");

	const [password, setPassword] = useState("");
	const [passwordVisibility, setPasswordVisibility] = useState(false);

	const promiseUserSelection = () => {
		return new Promise((done, reject) => {
			const onAction = (v, key) => {
				done(v);
				closeSnackbar(key);
			}

			enqueueSnackbar("Do you want to continue online?", {
				action: key => (
					<>
						<button
							type="button"
							onClick={() => onAction(true, key)}
							className="btn text-success btn-sm shadow-none font-size-12"
						>
							Online
						</button>
						<button
							type="button"
							onClick={() => onAction(false, key)}
							className="btn text-danger btn-sm shadow-none font-size-12"
						>
							Offline
						</button>
					</>
				),
				anchorOrigin: {
					horizontal: 'center',
					vertical: 'bottom',
				},
				autoHideDuration: 60000,
				onExited: () => reject(),
				onAbort: () => reject()
			});
		});
	}

	const login = async (e) => {
		e.preventDefault();

		const [isValidInputs, getErrors] = validation();

		if (isValidInputs) {
			setSubmitting(true);

			promiseUserSelection()
				.then(async (synchronies) => {
					const { data } = await axios.post(loginAPI, {
						email: email,
						password: password,
						sync: synchronies,
					});

					if (!data.succeeded) throw new Error(data.message || "Something went wrong");

					const result = data['data'];

					Cookies.set('client_id', result['accessToken'], {
						path: '/',
						expires: 30,
					});
					setUser(result);

					setAuthorized(true);
				})
				.catch(e => {
					if (typeof e === 'object') {
						enqueueSnackbar(<span>{e.message}</span>, {
							variant: 'error',
						})
					}

					setSubmitting(false);
				});
		} else {
			setErrors(getErrors);
		}
	}

	const validation = () => {
		let coupleErrors = {};

		if (!email.match(/^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/)) {
			coupleErrors = { ...coupleErrors, email: ['Email must be a valid email address'] };
		}

		if (password.length < 8) {
			coupleErrors = { ...coupleErrors, password: ['Password must contain at least 8 characters'] };
		}

		return [!(Object.keys(coupleErrors).length > 0), coupleErrors];
	}

	return (
		<Auth uniqueKey="login">
			<Helmet>
				<title>Log in</title>
			</Helmet>

			<div className={`${classes.form} position-absolute translate-middle rounded-3 start-50 pt-4 pb-3 px-4 m-0`} >
				<form onSubmit={login} method="get">
					<div className="text-center w-100 m-0 p-0">
						<h4 className="font-size-18 font-weight-600 mb-4 mt-0 mx-0 p-0">Log In</h4>
					</div>

					<div className="d-flex flex-column mw-100 w-100 mt-1 mb-0 mx-0 p-0">
						<div className={`input-group${errors.email ? ' is-invalid' : ''} rounded-3 overflow-hidden w-100 mb-3 mt-0 mx-0 p-0`}>
							<input
								type="email"
								placeholder="Enter your email"
								value={email}
								onChange={e => setEmail(e.target.value)}
								className="flex-grow-1 font-weight-500 rounded-0 border-0 px-3 py-0 m-0"
							/>
							<span className="flex-grow-0 d-flex align-items-center justify-content-center input-group-text border-0 py-0 px-2 m-0">
								<svg width="18" height="18" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 496 512">
									<path fill="currentColor" d="M248 8C111 8 0 119 0 256s111 248 248 248 248-111 248-248S385 8 248 8zm0 96c48.6 0 88 39.4 88 88s-39.4 88-88 88-88-39.4-88-88 39.4-88 88-88zm0 344c-58.7 0-111.3-26.6-146.5-68.2 18.8-35.4 55.6-59.8 98.5-59.8 2.4 0 4.8.4 7.1 1.1 13 4.2 26.6 6.9 40.9 6.9 14.3 0 28-2.7 40.9-6.9 2.3-.7 4.7-1.1 7.1-1.1 42.9 0 79.7 24.4 98.5 59.8C359.3 421.4 306.7 448 248 448z"></path>
								</svg>
							</span>
						</div>

						<div className={`input-group ${errors.password ? ' is-invalid' : ''} rounded-3 overflow-hidden mx-100 w-100 mb-4 mt-0 mx-0 p-0`}>
							<input
								type={passwordVisibility ? 'text' : 'password'}
								placeholder="Enter your password"
								value={password}
								onChange={e => setPassword(e.target.value)}
								className="flex-grow-1 font-weight-500 border-0 px-3 py-0 m-0"
							/>

							<button
								type="button"
								onClick={() => setPasswordVisibility(!passwordVisibility)}
								className="flex-grow-0 d-flex align-items-center justify-content-center input-group-text border-0 py-0 px-2 m-0"
							>
								<svg width="18" height="18" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 512">
									{passwordVisibility
										?
										<path fill="currentColor" d="M320 400c-75.85 0-137.25-58.71-142.9-133.11L72.2 185.82c-13.79 17.3-26.48 35.59-36.72 55.59a32.35 32.35 0 0 0 0 29.19C89.71 376.41 197.07 448 320 448c26.91 0 52.87-4 77.89-10.46L346 397.39a144.13 144.13 0 0 1-26 2.61zm313.82 58.1l-110.55-85.44a331.25 331.25 0 0 0 81.25-102.07 32.35 32.35 0 0 0 0-29.19C550.29 135.59 442.93 64 320 64a308.15 308.15 0 0 0-147.32 37.7L45.46 3.37A16 16 0 0 0 23 6.18L3.37 31.45A16 16 0 0 0 6.18 53.9l588.36 454.73a16 16 0 0 0 22.46-2.81l19.64-25.27a16 16 0 0 0-2.82-22.45zm-183.72-142l-39.3-30.38A94.75 94.75 0 0 0 416 256a94.76 94.76 0 0 0-121.31-92.21A47.65 47.65 0 0 1 304 192a46.64 46.64 0 0 1-1.54 10l-73.61-56.89A142.31 142.31 0 0 1 320 112a143.92 143.92 0 0 1 144 144c0 21.63-5.29 41.79-13.9 60.11z"></path>
										:
										<path fill="currentColor" d="M572.52 241.4C518.29 135.59 410.93 64 288 64S57.68 135.64 3.48 241.41a32.35 32.35 0 0 0 0 29.19C57.71 376.41 165.07 448 288 448s230.32-71.64 284.52-177.41a32.35 32.35 0 0 0 0-29.19zM288 400a144 144 0 1 1 144-144 143.93 143.93 0 0 1-144 144zm0-240a95.31 95.31 0 0 0-25.31 3.79 47.85 47.85 0 0 1-66.9 66.9A95.78 95.78 0 1 0 288 160z"></path>
									}
								</svg>
							</button>
						</div>

						<div className="btn-group w-100 mb-1 mt-0 mx-0 p-0">
							<button
								disabled={submitting}
								className="btn d-flex align-items-center justify-content-center shadow-none rounded text-center font-size-14 font-weight-500 w-100 m-0"
								type="submit"
							>
								{submitting
									? <CircularProgress color="secondary" size={20} />
									: 'Log In'
								}
							</button>
						</div>
					</div>
				</form>
			</div>
		</Auth>
	);
};

export default connect(null, {
	setAuthorized,
	setUser,
})(Login);