import { useState, useEffect } from 'react';

// Components
import Page from './templates/Page';
import Buildings from 'src/components/layout/Container/Buildings/Buildings';
import BuildingFacades from 'src/components/common/modals/BuildingFacades';
import RelayBtn from 'src/components/common/buttons/RelayBtn';
import { useSelector } from 'react-redux';
import { Helmet } from 'react-helmet';
import { useMemo } from 'react';

const getRelays = (state) => state.relays;
const Relay = () => {
	const relays = useSelector(getRelays);

	const [openBuildingFacadesModal, setOpenBuildingFacadesModal] = useState(false);

	const [building, setBuilding] = useState(null);

	const handleClickBuilding = (building) => {
		setBuilding(building);
		setOpenBuildingFacadesModal(true);
	}

	const buildingRelays = useMemo(() => {
		if (!building || !Array.isArray(relays)) return [];

		return relays.filter(s => s.building_id === building.id)
	}, [building, relays]);

	return (
		<Page uniqueKey="light">
			<Helmet>
				<title>Relay | Service</title>
			</Helmet>

			<Buildings
				onClickBuilding={handleClickBuilding}
			/>

			{(openBuildingFacadesModal && building) &&
				<BuildingFacades
					building={building}
					open={openBuildingFacadesModal}
					onClose={() => setOpenBuildingFacadesModal(false)}
				>
					{buildingRelays.map((relay, i) => (
						<RelayBtn
							key={i}
							id={relay.id}
							x={relay.location[0]}
							y={relay.location[1]}
							masterId={relay.master_id}
							serviceId={relay.single_id}
							is_group={relay.group_id !== null}
							dim={relay.dim}
						/>
					))}
				</BuildingFacades>
			}
		</Page>
	);
}

export default Relay;