import axios from 'src/api/axios';
import * as api from 'src/api/routes';

export const setMqtt = (connection) => {
	return { type: 'SET_MQTT_CONNECTION', payload: connection }
}

export const setAuthorized = (status) => {
	return { type: 'SET_AUTHORIZED_STATUS', payload: status }
}

export const setUser = (user) => {
	return { type: 'SET_USER_INFORMATION', payload: user }
}

export const fetchUser = () => async (dispatch) => {
	const { data } = await axios.get(api.user);

	if (!data.succeeded) throw new Error(data.errors[0]);

	dispatch({ type: 'SET_USER_INFORMATION', payload: data.data });
}

