export const addJob = (type, id, payload) => async (dispatch) => {
	const { data } = await axios.post(api.jobs.create(type, id), payload);

	if (!data.succeeded) throw new Error(data.errors);
	dispatch({ type: 'ADD_JOB', payload: data.data });
}

export const deleteJob = (id) => async (dispatch) => {
	const { data } = await axios.delete(api.jobs.delete(id));

	if (!data.succeeded) throw new Error(data.errors);
	dispatch({ type: 'DELETE_JOB', payload: id });
}

export const addMood = (buildingId, payload) => async (dispatch) => {
	const { data } = await axios.post(api.moods.create(buildingId), payload);

	if (!data.succeeded) throw new Error(data.errors);
	dispatch({ type: 'ADD_MOOD', payload: data.data });
}

export const changeMoodName = (moodId, name) => ({ type: 'CHANGE_MOOD_NAME', payload: { id: moodId, name } });

export const deleteMood = (moodId) => async (dispatch) => {
	const { data } = await axios.delete(api.moods.delete(moodId));

	if (!data.succeeded) throw new Error(data.errors);
	dispatch({ type: 'DELETE_MOOD', payload: moodId });
}

export const setLights = (lights) => ({ type: 'SET_LIGHT', payload: lights });

export const setCurtains = (curtains) => ({ type: 'SET_CURTAIN', payload: curtains });

export const setRelays = (relays) => ({ type: 'SET_RELAY', payload: relays });

export const setThermostats = (th) => ({ type: 'SET_THERMOSTATS', payload: th });

/* API Request */
export const updateDim = async (id, value, type) => {
	try {
		value = Array.isArray(value) ? value.join(',') : value;
		const { data } = await axios.patch(api.setDim, { id, value, type });

		if (!data.succeeded) throw new Error("Something went wrong");
	} catch (e) {
		console.log(e);
	}
};

export const updateGroupDim = async (values) => {
	try {
		const { data } = await axios.patch(api.setGroupDim, values);

		if (!data.succeeded) throw new Error("Something went wrong");
	} catch (e) {
		console.log(e);
	}
};

export const updateThermostat = async (id, params) => {
	try {
		const { data } = await axios.patch(api.thermostats.update(id), params);
		if (!data.succeeded) throw new Error("Something went wrong");
	} catch (e) {
		console.log(e);
	}
};