import React from 'react';
import { motion } from 'framer-motion';

const Page = ({ uniqueKey, children }) => {
	return (
		<motion.main
			key={uniqueKey}
			initial={{
				x: 200,
				opacity: 0,
			}}
			animate={{
				x: 0,
				opacity: 1,
			}}
			exit={{
				x: -200,
				opacity: 0,
			}}
			transition={{
				opacity: { duration: .25 },
				y: { duration: .3 },
				default: { duration: .25 },
			}}
		>
			<div style={{minHeight: 'calc(100vh - 128px)'}} className="container">
				{children}
			</div>
		</motion.main>
	);
}

export default Page;
