import React from 'react';
import { connect } from 'react-redux';

// Components
import Building from 'src/components/layout/Container/Buildings/Building';

const Buildings = ({ buildings, onClickBuilding, children }) => {
	return (
		<div className="row justify-content-center mx-auto my-0">
			{buildings.map((building, i) => (
				<Building
					key={i}
					{...building}
					onClick={() => onClickBuilding(building)}
					id={building.id}
					name={building.name}
					type={building.type}
					image={building.image}
				/>
			))}

			{children}
		</div>
	);
}

const mapStateToProps = ({ buildings }) => {
	return { buildings: buildings };
}

export default connect(mapStateToProps)(Buildings);
