import React from 'react';
import { useSelector } from 'react-redux';

// Components
import Page from '../templates/Page';
import Mood from 'src/components/layout/Container/Moods/Mood';
import { useParams } from 'react-router-dom';
import { useMemo } from 'react';
import MoodBuildings from './Buildings';
import Create from 'src/components/layout/Container/Moods/Create';

const getMoods = state => ({ moods: state.moods, buildings: state.buildings });
const Moods = () => {
	const params = useParams();

	const { moods, buildings } = useSelector(getMoods);

	const building = useMemo(() => buildings.find(building => +building.id === +params.buildingId), [buildings, params.buildingId]);

	const filteredMoods = useMemo(() => moods.filter(mood => +mood.building_id === +params.buildingId), [moods, params.buildingId]);

	if (!building) return null;

	return (
		<Page uniqueKey="scene">
			<div className="row justify-content-center mx-auto my-0">
				{filteredMoods.map((mood, i) => (
					<Mood
						key={i}
						id={mood.id}
						name={mood.name}
						services={mood.services}
					/>
				))}

				<Create building={building} />
			</div>
		</Page>
	);
};

export { MoodBuildings };
export default Moods;