import React, { useEffect, useMemo } from 'react';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';
import { makeStyles, createStyles } from '@material-ui/styles';
import { alpha } from '@material-ui/core';

import { SERVICES } from 'src/constants/app';

// Components
import Page from './templates/Page';
import { useDispatch, useSelector } from 'react-redux';
import { setLights, updateGroupDim } from 'src/redux/actions';
import { useRef } from 'react';
import clsx from 'clsx';

const useStyles = makeStyles(theme => createStyles({
	service: {
		width: '228px',
		height: '250px',
		maxWidth: '90%',
		margin: '16px',

		'&>a, &>button': {
			borderRadius: '4px',
			border: `2px solid ${theme.palette.primary.light}`,
			backgroundColor: 'transparent',
			color: theme.palette.secondary.main,
			transition: theme.transitions.create(['all'], {
				duration: '250ms',
			}),

			'& svg': {
				width: '40%',
				height: '40%',
			},

			'& h1': {
				color: theme.palette.gray[400],
			},

			'&:hover, &:active': {
				transform: 'scale(1.1)',
				backgroundColor: theme.palette.primary.light,
				boxShadow: `0px 4px 5px -2px ${alpha(theme.palette.primary.light, .2)},0px 7px 10px 1px ${alpha(theme.palette.primary.light, .14)},0px 2px 16px 1px ${alpha(theme.palette.primary.light, .12)}`,
			},
		},

		[theme.breakpoints.down('lg')]: {
			width: '200px',
			height: '200px',
		}
	}
}));

const getStates = state => ({ lights: state.services.lights, mqtt: state.mqtt, project_id: state.user?.project_id });
const Home = () => {
	const subscribed = useRef(null);

	const classes = useStyles();

	const { mqtt, lights, project_id } = useSelector(getStates);

	const dispatch = useDispatch();

	const availableMastersIds = useMemo(() => {
		const mastersIds = [];

		lights.forEach(light => {
			mastersIds.push(light.master_id);
		});

		return [...new Set(mastersIds)];
	}, [lights]);

	const onReceiveData = (payload) => {
		try {
			if (!Array.isArray(lights) || lights.length === 0) return;

			/* Modified */
			const lines = payload.split("\n");

			if (String(lines[1]).trim() !== 'FAST_SCAN') return;

			lines.splice(0, 8);

			/* Find lights */
			const copyOfLights = [...lights];
			let i = 0;
			while (i < lines.length - 1) {
				const hexDecimalDim = lines[i + 1].substr(3);
				const hexDecimalId = lines[i + 3].substr(3);

				if (!hexDecimalDim.includes("N/A")) {
					const id = parseInt(hexDecimalId, 10);
					const dim = parseInt(hexDecimalDim, 16);

					if (!isNaN(dim)) {
						copyOfLights.forEach((light, index) => {
							if (light.single_id && light.single_id.find(lightId => +lightId === +id)) {
								copyOfLights[index].value = dim;
							}
						});
					}
				}

				i = i + 5;
			}

			/* Update DB */
			updateGroupDim(copyOfLights);

			/* Update */
			dispatch(setLights(copyOfLights));
		} catch (e) {
			console.log(e);
		}
	};

	const sendCommands = (index, command) => {
		mqtt.publish(`${project_id}/${availableMastersIds[index]}/Dali/In`, command);

		if (index + 1 < availableMastersIds.length) setTimeout(() => sendCommands(index + 1, availableMastersIds), 1500);
	};

	const sendQuickScanCommand = () => {
		if (!mqtt) return;

		const command = Buffer.from(`{"CMD\r\n":"FAST_SCAN\r\n","BROADCAST\r\n":"ALL\r\n"}`);
		sendCommands(0, command);
	};

	const runFastScan = () => {
		if (!mqtt) return;

		sendQuickScanCommand();

		setInterval(sendQuickScanCommand, 300000);
	};

	useEffect(() => {
		if (subscribed.current === true || !mqtt || availableMastersIds.length === 0) return;

		const topics = [];
		availableMastersIds.forEach(id => {
			const topic = `${project_id}/${id}/Dali/Out`;

			topics.push(topic);
			mqtt.subscribe(topic);
		});

		subscribed.current = true;

		mqtt.on('message', (topic, payload) => {
			if (!topics.includes(topic)) return;

			payload = payload.toString();
			onReceiveData(payload);
		});
	}, [mqtt, availableMastersIds, subscribed]);

	return (
		<Page uniqueKey="dashboard">
			<Helmet>
				<title>Dashboard</title>
			</Helmet>

			<div className="row justify-content-center mx-auto my-0">
				{SERVICES.map((service, i) => (
					<div key={i} title={service.name} className={clsx(classes.service, 'p-0')}>
						<Link className="d-flex flex-column align-items-center justify-content-center w-100 h-100 p-0 m-0" to={service.path}>
							{service.icon}
							<h1 className="font-size-16 font-weight-500 mt-4 mb-0 mx-auto p-0">{service.name}</h1>
						</Link>
					</div>
				))}
			</div>

			<div style={{ display: 'flex', gap: '16px', margin: '0 auto', textAlign: 'center' }}>
				<button
					type='button'
					onClick={runFastScan}
					style={{
						border: 0,
						backgroundColor: '#ff5951',
						borderRadius: '4px',
						fontSize: '12px',
						fontWeight: 400,
						letterSpacing: 1,
						padding: '12px 24px',
						cursor: 'pointer',
						display: 'flex',
						alignItems: 'center',
						justifyContent: 'center',
						color: '#fff',
						margin: '24px auto'
					}}
				>
					START FAST SCAN
				</button>
			</div>
		</Page>
	);
}

export default Home;