import { makeStyles, createStyles } from '@material-ui/styles';
import { alpha } from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { useMemo, useState } from 'react';
import useLongPress from 'src/hooks/useLongPress';
import { createPortal } from 'react-dom';
import { setDaliBroadcastDim, setGroupDaliLightDim, setGroupTempLightDim, setRGBGroupDim, setRGBSingleDim, setSingleDaliLightDim, setSingleTempLightDim } from 'src/libs/commands';
import { changeMoodName, deleteMood, updateGroupDim } from 'src/redux/actions';
import { useSnackbar } from 'notistack';
import ContextMenu from 'src/components/common/ContextMenu';
import AutoGenerateFormModal from 'src/components/common/modals/Form';
import axios from 'src/api/axios';
import * as api from 'src/api/routes';

const useStyles = makeStyles(theme => createStyles({
	mood: {
		position: 'relative',
		width: '256px',
		height: '170px',
		borderRadius: '4px',
		boxShadow: `0px 3px 3px -2px ${alpha(theme.palette.primary.main, .2)},0px 3px 4px 0px ${alpha(theme.palette.primary.main, .14)},0px 1px 8px 0px ${alpha(theme.palette.primary.main, .12)}`,
		margin: '16px',
		border: `2px dashed ${theme.palette.gray[600]}`,

		'&>button': {
			border: 'none',
			outline: 'none',
			backgroundColor: 'rgba(255, 255, 255, 0.1)',
			cursor: 'pointer',
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			textTransform: 'uppercase',
			fontSize: '16px',
			letterSpacing: '1px',
			color: theme.palette.gray[50]
		}
	},

	wrapper: {
		position: 'absolute',
		width: '100%',
		height: '100%',
		backgroundColor: 'rgba(0, 0, 0, 0.5)',
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',

		'& span': {
			fontSize: '14px',
			fontWeight: 500
		}
	}
}));

const getState = ({ user, mqtt, services }) => ({ project_id: user?.project_id, mqtt, allServices: [...services.lights, ...services.curtains, ...services.relays] });
const Mood = ({ id, name, services }) => {
	const classes = useStyles();
	
	
	const dispatch = useDispatch();

	const { project_id, mqtt, allServices } = useSelector(getState);

	const { enqueueSnackbar } = useSnackbar();

	const [run, setRun] = useState(false);
	
	const [contextMenuIsOpen, setContextMenuIsOpen] = useState(false);

	const [nameModalIsOpen, setNameModalIsOpen] = useState(false);

	const getServicesById = useMemo(() => {
		return services.map(({ service_id, dim }) => {
			const service = allServices.find(({ id }) => id === service_id);
			return { ...service, value: dim };
		});
	}, [services, allServices]);
	
	const availableMastersIds = useMemo(() => {
		const mastersIds = [];

		getServicesById.forEach(service => mastersIds.push(service.master_id));

		return [...new Set(mastersIds)].filter(Boolean);
	}, [getServicesById]);

	const turnOffMaster = (cb, index = 0) => {
		try {
			if (availableMastersIds.length === 0) return cb();

			mqtt.publish(`${project_id}/${availableMastersIds[index]}/Dali/In`, setDaliBroadcastDim('OFF', 1));

			if (availableMastersIds[index + 1]) return setTimeout(() => turnOffMaster(index + 1), 1000);

			setTimeout(cb, 1000);
		} catch (e) {
			setTimeout(cb, 1000);
		}
	};

	const publish = (type, service) => {
		const isGroup = service.is_group;
		const groupId = service.groupId;
		const singleId = service.single_id[0];

		if (type === 'dali_light') {
			const dim = isGroup ? setGroupDaliLightDim(service.value, groupId) : setSingleDaliLightDim(service.value, singleId);
			return mqtt.publish(`${project_id}/${service.master_id}/Dali/In`, dim);
		}

		if (type === 'rgb') {
			const dim = isGroup ? setGroupDaliLightDim(service.value[4], groupId) : setSingleDaliLightDim(service.value[4], singleId);
			mqtt.publish(`${project_id}/${service.master_id}/Dali/In`, dim);

			const hex = `${service.value[0].toString(16)}${service.value[1].toString(16)}${service.value[2].toString(16)}`;
			const dec = parseInt(hex, 16);
			const rgbVal = isGroup ? setRGBGroupDim(dec, groupId) : setRGBSingleDim(dec, singleId);
			return mqtt.publish(`${project_id}/${service.master_id}/Dali/In`, rgbVal);
		}

		if (type === 'cct') {
			const [cw, brightness] = service.value;

			const dim = isGroup ? setGroupDaliLightDim(brightness, groupId) : setSingleDaliLightDim(brightness, singleId);
			mqtt.publish(`${project_id}/${service.master_id}/Dali/In`, dim);

			const value = isGroup ? setGroupTempLightDim(cw, groupId) : setSingleTempLightDim(cw, singleId);
			return mqtt.publish(`${project_id}/${service.master_id}/Dali/In`, value);
		}

		if (type === 'curtain') {
			const message = `{"id":"${singleId}","command":"${(!!service.value) ? 'open' : 'close'}"}`;
			return mqtt.publish(`${project_id}/Curtain/In`, Buffer.from(message));
		}

		if (type === 'relay') return;
	};

	const publishOneToOne = (index = 0) => {
		try {
			const service = getServicesById[index];
			publish(service.type, service);

			const nextIndex = index + 1;
			if (nextIndex < getServicesById.length) setTimeout(() => publishOneToOne(nextIndex), 1000);
			else setRun(false);
		} catch (e) {
			enqueueSnackbar('Something went wrong!', { variant: 'error' });
			console.log(e);
			setRun(false);
		}
	};

	const overallPublish = () => {
		if (!mqtt || !getServicesById.length) return;

		/* RUN */
		setRun(true);
		updateGroupDim(getServicesById);

		turnOffMaster(publishOneToOne);
	}

	const removeMood = () => {
		setContextMenuIsOpen(false);
		dispatch(deleteMood(id));

		enqueueSnackbar("Mood deleted successfully.", {
			key: 'deleted',
			variant: 'success'
		});
	};

	const onChangeName = async (_e, values) => {
		try {
			const { data } = await axios.patch(api.moods.update(id), {
				name: values.name
			});

			if (!data.succeeded) throw new Error(data.errors[0]);

			dispatch(changeMoodName(id, values.name));
			enqueueSnackbar("Mood updated successfully.", {
				variant: 'success'
			});
		} catch (e) {
			console.log(e);
		}
	};

	const toggleContextMenu = () => setContextMenuIsOpen(!contextMenuIsOpen);

	const longPressEvent = useLongPress(
		toggleContextMenu,
		overallPublish,
		{
			delay: 750
		}
	);

	return (
		<div className={`${classes.mood} position-relative overflow-hidden p-0`}>
			{run
				?
				<div className={classes.wrapper}>
					<span>Mood is ready</span>
				</div>
				:
				<button
					{...longPressEvent}
					type="button"
					className="position-relative w-100 h-100 p-0 m-0"
				>
					{name}
				</button>
			}

			{contextMenuIsOpen &&
				createPortal(<ContextMenu
					list={[
						{ title: 'Run', onClick: overallPublish },
						/* {
							title: 'Runtime', onClick: () => {
								setContextMenuIsOpen(false);
								setRuntimeModalIsOpen(true);
							}
						}, */
						{
							title: 'Change name', onClick: () => {
								setContextMenuIsOpen(false);
								setNameModalIsOpen(true);
							}
						},
						{ title: 'Delete', onClick: removeMood },
						{ title: 'Cancel', onClick: () => setContextMenuIsOpen(false) },
					]}
				/>, document.body)}

			<AutoGenerateFormModal
				open={nameModalIsOpen}
				onClose={() => setNameModalIsOpen(false)}
				onSubmit={onChangeName}
				inputs={[
					{
						autoFocus: true,
						required: true,
						type: 'text',
						name: 'name',
						label: 'Name',
						placeholder: 'Enter new mood name'
					}
				]}
			/>
		</div>
	);
};

export default Mood;
