import React from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { Link, useHistory } from "react-router-dom";
import Clock from './Clock';

import * as routes from 'src/constants/routes';

// Icons
import { QuestionCircleSVG, ArrowLeftSVG, HomeSVG, LogoutSVG } from 'src/icons';
import { useSelector } from 'react-redux';

const useStyles = makeStyles(theme => createStyles({
	header: {
		minHeight: '64px',
		maxHeight: '64px',
	},

	navbar: {
		'& ul>li': {
			'&>a, &>button': {
				display: 'flex',
				alignItems: 'center',
				justifyContent: 'center',
				padding: '12px',
				color: theme.palette.gray[400],

				'&:active, &:hover': {
					color: theme.palette.gray[100],
				},

				'&:disabled,&[disabled]': {
					pointerEvents: 'none',
					cursor: 'not-allowed',
					color: theme.palette.gray[600],
				},
			}
		}
	}
}));

const getUser = state => state.user;
const Header = () => {
	const classes = useStyles();

	const history = useHistory();

	const user = useSelector(getUser);

	const redirectToPreviousPage = () => {
		history.goBack();
	}

	return (
		<header className={`${classes.header} d-flex align-items-center mb-2 mt-0 mx-0 p-0`}>
			<nav className={`${classes.navbar} d-flex justify-content-between align-items-center w-100 h-100`}>
				<ul className="list-group list-group-horizontal list-unstyled align-items-center p-0 m-0">
					<li>
						<button className="btn" onClick={redirectToPreviousPage}>
							<ArrowLeftSVG width="20" height="20" />
						</button>
					</li>
					<li>
						<Link disabled={history.location.pathname === routes.dashboard} className="btn" to={routes.dashboard}>
							<HomeSVG width="24" height="24" />
						</Link>
					</li>
					<li>
						<span
							style={{
								color: 'rgba(255, 255, 255, .6)',
								fontSize: '14px',
								fontWeight: 500
							}}
						>
							#{user.project_id}
						</span>
					</li>
				</ul>

				<Clock />

				<ul className="list-group list-group-horizontal list-unstyled align-items-center p-0 m-0">
					<li>
						<button className="btn">
							<QuestionCircleSVG width="24" height="24" />
						</button>
					</li>
					<li>
						<Link disabled={history.location.pathname === routes.logout} className="btn" to={routes.logout}>
							<LogoutSVG width="24" height="24" />
						</Link>
					</li>
				</ul>
			</nav>
		</header>
	);
}

export default Header;