import React from 'react';
import { motion } from 'framer-motion';

const Auth = ({ uniqueKey, children }) => {
	return (
		<motion.div
			key={uniqueKey}
			initial={{
				y: 200,
				opacity: 0,
			}}
			animate={{
				y: 0,
				opacity: 1,
			}}
			exit={{
				y: -200,
				opacity: 0,
			}}
			transition={{
				opacity: { duration: .25 },
				y: { duration: .3 },
				default: { duration: .25 },
			}}
		>
			{children}
		</motion.div>
	);
}

export default Auth;
