import { useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { useDispatch } from 'react-redux';
import { setLights, updateDim } from 'src/redux/actions';
import useLongPress from 'src/hooks/useLongPress';

// Components
import { CCTModal } from '../modals/Services';

// Icons
import { CCTSVG } from 'src/icons';
import { useSelector } from 'react-redux';
import { setGroupDaliLightDim, setGroupTempLightDim, setSingleDaliLightDim, setSingleTempLightDim } from 'src/libs/commands';

const useStyles = makeStyles(theme => createStyles({
	light: {
		width: '48px',
		height: '48px',
		backgroundColor: theme.palette.warning.main,
		boxShadow: `0px -4px 12px ${theme.palette.secondary.main}`,
		transition: theme.transitions.create(['all']),

		'& svg': {
			width: '24px',
			height: '24px',
		},

		[theme.breakpoints.down('md')]: {
			width: '36px',
			height: '36px',
	
			'& svg': {
				width: '20px',
				height: '20px',
			},
		},

		[theme.breakpoints.down('sm')]: {
			width: '28px',
			height: '28px',
	
			'& svg': {
				width: '18px',
				height: '18px',
			},
		}
	}
}));

const getStates = state => ({ lights: state.services.lights, mqtt: state.mqtt, project_id: state.user?.project_id });
const CCTBtn = ({ id, is_group, dim, serviceId, groupId, masterId, x, y, type, onRun }) => {
	const classes = useStyles();

	const { lights, mqtt, project_id } = useSelector(getStates);
	const dispatch = useDispatch();

	const [openModal, setOpenModal] = useState(false);

	const publishDT6 = (id, brightness, cw) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		if (+brightness !== +dim[1]) {
			const dim = is_group ? setGroupDaliLightDim(brightness, groupId) : setSingleDaliLightDim(brightness, id);
			mqtt.publish(`${project_id}/${masterId}/Dali/In`, dim);
		}

		if (!cw) return;
		const value = is_group ? setGroupTempLightDim(cw, id) : setSingleTempLightDim(cw, id);
		mqtt.publish(`${project_id}/${masterId}/Dali/In`, value);
	}
	
	const publishDT8 = (id, brightness, cw) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		if (+brightness !== +dim[1]) {
			const dim = is_group ? setGroupDaliLightDim(brightness, groupId) : setSingleDaliLightDim(brightness, id);
			mqtt.publish(`${project_id}/${masterId}/Dali/In`, dim);
		}

		if (!cw) return;
		const value = is_group ? setGroupTempLightDim(cw, id) : setSingleTempLightDim(cw, id);
		mqtt.publish(`${project_id}/${masterId}/Dali/In`, value);
	}

	const onChangeBrightness = (brightness, cw) => {
		const value = cw ? [cw, brightness] : [+dim[0], brightness];
		
		updateDim(id, value, type);
		onRun?.({ id, is_group, value, serviceId, groupId, masterId, x, y });
		
		const daliType = type.split('.')[1];
		if (daliType === 'dt6') publishDT6(is_group ? groupId : serviceId, brightness, cw);
		else publishDT8(is_group ? groupId : serviceId, brightness, cw);

		const newLights = lights.map(light => {
			if (light.id === id) return { ...light, value: value };

			return light;
		});
		dispatch(
			setLights(newLights)
		);
	}

	const longPressEvent = useLongPress(
		() => setOpenModal(true),
		() => onChangeBrightness(+dim[1] < 127 ? 254 : 0, undefined),
		{
			delay: 1000
		}
	);

	return (
		<>
			<button
				{...longPressEvent}
				style={{ left: `${x}%`, top: `${y}%`, opacity: ((((+dim[1] / 254) * 60) + 40) / 100) }}
				className={`${classes.light} btn text-light position-absolute rounded-circle p-0 m-0`}
				type="button"
			>
				<CCTSVG />
			</button>

			{openModal &&
				<CCTModal
					onClose={() => setOpenModal(false)}
					onChange={onChangeBrightness}
					open={openModal}
					initialBrightness={+dim[1]}
					initialCct={dim[0]}
				/>
			}
		</>
	);
};

export default CCTBtn;