import React from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { alpha } from '@material-ui/core';

const useStyles = makeStyles(theme => createStyles({
	building: {
		width: '256px',
		height: '170px',
		borderRadius: '4px',
		boxShadow: `0px 3px 3px -2px ${alpha(theme.palette.primary.main, .2)},0px 3px 4px 0px ${alpha(theme.palette.primary.main, .14)},0px 1px 8px 0px ${alpha(theme.palette.primary.main, .12)}`,
		margin: '16px',

		'& .bg-image': {
			background: `${theme.palette.gray[800]} no-repeat scroll center`,
			backgroundSize: 'cover',
			borderRadius: '4px',
			filter: 'brightness(.6)',
			WebkitFilter: 'brightness(.6)',
		},

		'&>button': {
			'&>ul>li': {
				margin: '0 16px 0 0',
				color: theme.palette.gray[100],
				verticalAlign: 'middle',

				'& svg': {
					width: '14px',
					height: '14px',
				}
			}
		}
	},
}));

const Building = ({ id, name, type, image, ...props }) => {
	const classes = useStyles();

	return (
		<div className={`${classes.building} position-relative overflow-hidden p-0`}>
			<div style={{ backgroundImage: `url('${image}')` }} className="bg-image position-absolute start-0 top-0 w-100 h-100 p-0 m-0" />

			<button
				{...props}
				type="button"
				className="btn d-flex flex-column justify-content-between position-relative w-100 h-100 p-0 m-0"
			>
				<div className="text-start w-100 p-2 m-0">
					<h1 className="font-size-16 font-weight-500 text-white p-0 m-0">{name}</h1>
					<span className="font-size-14 font-weight-400 text-light text-capitalize p-0 m-0">{type}</span>
				</div>
			</button>
		</div>
	);
};

export default Building;