import { useState, useMemo, Fragment } from 'react';
import { useSelector } from 'react-redux';

// Components
import Page from './templates/Page';
import Buildings from 'src/components/layout/Container/Buildings/Buildings';
import BuildingFacades from 'src/components/common/modals/BuildingFacades';
import LightBtn from 'src/components/common/buttons/LightBtn';
import RGBBtn from 'src/components/common/buttons/RGBBtn';
import CCTBtn from 'src/components/common/buttons/CCTBtn';
import { Helmet } from 'react-helmet';

const BTN = ({ type, children, ...props }) => {
	const typeName = type.split('.');

	if (typeName[0] === 'rgb') return <RGBBtn type={type} {...props} />;
	if (typeName[0] === 'cct') return <CCTBtn type={type} {...props} />;

	return <LightBtn type={type} {...props} />;
};

const getLights = state => state.services.lights;
const Light = () => {
	const lights = useSelector(getLights);

	const [openBuildingFacadesModal, setOpenBuildingFacadesModal] = useState(false);

	const [building, setBuilding] = useState(null);

	const buildingLights = useMemo(() => {
		if (!building || !Array.isArray(lights)) return [];
		
		return lights.filter(s => s.building_id === building.id);
	}, [lights, building]);

	const handleClickBuilding = (building) => {
		setBuilding(building);
		setOpenBuildingFacadesModal(true);
	};

	return (
		<Page uniqueKey="light">
			<Helmet>
				<title>Light | Service</title>
			</Helmet>

			<Buildings
				onClickBuilding={handleClickBuilding}
			/>

			{(openBuildingFacadesModal && building) &&
				<BuildingFacades
					building={building}
					open={openBuildingFacadesModal}
					onClose={() => setOpenBuildingFacadesModal(false)}
				>
					{buildingLights.map((light) => (
						<Fragment key={light.id}>
							<BTN
								type={light.type}
								id={light.id}
								x={light.location[0]}
								y={light.location[1]}
								dim={light.value}
								serviceId={light.service_id}
								masterId={light.master_id}
								is_group={light.group_id !== null}
								options={light.options}
								groupId={light.group_id}
							/>
						</Fragment>
					))}
				</BuildingFacades>
			}
		</Page>
	);
}

export default Light;