/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnimatePresence } from 'framer-motion';
import { Route, Switch, Redirect } from "react-router-dom";
import { useSnackbar } from 'notistack';
import mqtt from 'mqtt';
import { makeStyles, createStyles } from '@material-ui/styles';
import * as apiRoutes from 'src/api/routes';

import Header from 'src/components/layout/Header';
import Footer from 'src/components/layout/Footer';

// Fetch
import { fetchBuildings, fetchScenes, fetchServices, fetchMoods, fetchUser, setMqtt, fetchJobs, fetchThermostats } from 'src/redux/actions';

// Pages
import * as routes from "src/constants/routes";
import DashboardPage from "src/pages/Dashboard";
import LightPage from "src/pages/Light";
import CurtainPage from "src/pages/Curtain";
import RelayPage from "src/pages/Relay";
import ThermostatPage from "src/pages/Thermostat";
import ScenesPage, { SceneBuildings } from "src/pages/Scenes";
import MoodsPage, { MoodBuildings } from "src/pages/Moods";
import JobsPage from 'src/pages/Jobs';
import Modal from '../common/modals/Modal';
import axios from 'src/api/axios';

const useStyles = makeStyles(theme => createStyles({
	modal: {
		maxWidth: '90%',
		width: '400px',
		borderRadius: '24px',
		outline: 'none',
		backgroundColor: theme.palette.primary.main,
	},

	container: {
		display: 'flex',
		flexDirection: 'column',
		gap: '32px'
	},

	title: {
		textAlign: 'center',
		'& h2': {
			fontSize: '18px',
			fontWeight: 600,
			textTransform: 'uppercase',

			'& span': {
				color: theme.palette.secondary.main
			}
		}
	},

	form: {
		display: 'flex',
		flexDirection: 'column',
		gap: '16px'
	},

	input: {
		height: '48px',
		width: '100%',
		fontSize: '14px',
		fontWeight: 400,
		padding: '0 16px',
		color: theme.palette.gray[300],
		borderRadius: '8px',
		outline: 'none',
		backgroundColor: 'transparent',
		border: '1px solid ' + theme.palette.gray[800]
	},

	apply: {
		marginTop: '8px',
		height: '40px',
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		color: theme.palette.primary.main,
		fontSize: '12px',
		fontWeight: 500,
		cursor: 'pointer',
		textTransform: 'uppercase',
		outline: 'none',
		border: 'none',
		borderRadius: '8px',
		backgroundColor: theme.palette.secondary.main
	}
}));

const Layout = () => {
	const classes = useStyles();

	const [loading, setLoading] = useState(true);

	const [showMqttModal, setShowMqttModal] = useState(false);

	const [inputs, setInputs] = useState({
		ip: "",
		port: "1884"
	});

	const dispatch = useDispatch();

	const user = useSelector(state => state.user);

	const { enqueueSnackbar, closeSnackbar } = useSnackbar();

	const getRandomName = () => Math.random().toString(16).slice(2, 8);

	const setFieldValue = (name, value) => {
		setInputs({
			...inputs,
			[name]: value
		});
	}

	const connectToMQTT = (ip, port) => {
		let snackbar;
		const options = {
			port: Number(port),
			clientId: 'mqttjs_' + getRandomName(),
			keepalive: 20,
			clean: true,
			reconnectPeriod: 5 * 1000,
			connectTimeout: 5 * 1000,
		};

		const connection = mqtt.connect("ws://" + ip, options);

		connection.on('connect', () => {
			if (snackbar) closeSnackbar(snackbar);
		});

		connection.on('offline', () => {
			/* snackbar = enqueueSnackbar('No MQTT connection!', {
				variant: 'error',
				persist: true,
				preventDuplicate: true,
				anchorOrigin: {
					vertical: 'bottom',
					horizontal: 'center',
				},
				style: {
					bottom: {
						bottom: '96px'
					}
				}
			}); */
		});

		connection.on('close', () => connection.reconnect());

		dispatch(setMqtt(connection));
	}

	const onUpdateMQTT = (e) => {
		try {
			e.preventDefault();
			const { ip, port } = inputs;

			const { data } = axios.patch(apiRoutes.setMqtt, { ip, port });
			enqueueSnackbar('Updated successfully!', {
				variant: 'success'
			});

			setTimeout(() => window.location.reload(), 1500);
		} catch (e) {
			//
		}
	};

	useEffect(() => {
		dispatch(fetchUser())
			.then(dispatch(fetchBuildings()))
			.then(dispatch(fetchServices()))
			.then(dispatch(fetchThermostats()))
			.then(dispatch(fetchScenes()))
			.then(dispatch(fetchMoods()))
			.then(dispatch(fetchJobs()))
			.finally(() => {
				setLoading(false);
			})
	}, []);

	useEffect(() => {
		if (!user) return;

		try {
			if (!user.mqtt_ip || !user.mqtt_port) {
				setShowMqttModal(true);
				return;
			}

			connectToMQTT(user.mqtt_ip, user.mqtt_port);
		} catch (e) {
			console.log(e)
		}
	}, [user]);

	if (loading) return (
		<div className='position-absolute w-100 h-100 d-flex flex-column justify-content-center align-items-center'>
			<span>Please wait ...</span>
		</div>
	);

	if (showMqttModal) return (
		<Modal
			open={showMqttModal}
			onClose={() => null}
		>
			<div className={`${classes.modal} position-absolute translate-middle start-50 top-50 px-5 py-5 m-0`}>
				<div className={classes.container}>
					<div className={classes.title}>
						<h2>initial setup of <span>mqtt</span></h2>
					</div>

					<form method='get' onSubmit={onUpdateMQTT} className={classes.form}>
						<input
							required
							className={classes.input}
							type='text'
							value={inputs.ip}
							inputMode='numeric'
							placeholder='MQTT IP'
							onChange={(e) => setFieldValue('ip', e.target.value.replace(/[^0-9.]/ig, ''))}
						/>

						<input
							required
							className={classes.input}
							type='number'
							value={inputs.port}
							maxLength={4}
							inputMode='numeric'
							placeholder='MQTT PORT'
							onChange={(e) => setFieldValue('port', Number(e.target.value.replace(/[^0-9]/ig, '')))}
						/>

						<button type='submit' className={classes.apply}>apply</button>
					</form>
				</div>
			</div>
		</Modal>
	);

	return (
		<>
			<Header />

			<AnimatePresence exitBeforeEnter initial={false}>
				<Switch>
					<Route exact sensitive path={routes.dashboard} component={DashboardPage} />
					<Route exact sensitive path={routes.light} component={LightPage} />
					<Route exact sensitive path={routes.curtain} component={CurtainPage} />
					<Route exact sensitive path={routes.relay} component={RelayPage} />
					<Route exact sensitive path={routes.thermostat} component={ThermostatPage} />

					<Route exact sensitive path={routes.sceneBuildings} component={SceneBuildings} />
					<Route exact sensitive path={routes.scenes} component={ScenesPage} />

					<Route exact sensitive path={routes.moodBuildings} component={MoodBuildings} />
					<Route exact sensitive path={routes.moods} component={MoodsPage} />

					<Route exact sensitive path={routes.jobs} component={JobsPage} />

					<Route><Redirect to={routes.dashboard} /></Route>
				</Switch>
			</AnimatePresence>

			<Footer />
		</>
	);
};

export default Layout;
