import { useState, Fragment } from 'react';

// Components
import Page from './templates/Page';
import Buildings from 'src/components/layout/Container/Buildings/Buildings';
import BuildingFacades from 'src/components/common/modals/BuildingFacades';
import CurtainBtn from 'src/components/common/buttons/CurtainBtn';
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';
import { useMemo } from 'react';

const getCurtains = (state) => state.services.curtains;
const Curtain = () => {
	const curtains = useSelector(getCurtains);

	const [openBuildingFacadesModal, setOpenBuildingFacadesModal] = useState(false);

	const [building, setBuilding] = useState(null);

	const buildingCurtains = useMemo(() => {
		if (!building || !Array.isArray(curtains)) return [];
		
		return curtains.filter(s => s.building_id === building.id);
	}, [curtains, building]);

	const handleClickBuilding = (building) => {
		setBuilding(building);
		setOpenBuildingFacadesModal(true);
	}
	console.log(curtains);

	return (
		<Page uniqueKey="light">
			<Helmet>
				<title>Curtain | Service</title>
			</Helmet>

			<Buildings
				onClickBuilding={handleClickBuilding}
			/>

			{(openBuildingFacadesModal && building) &&
				<BuildingFacades
					building={building}
					open={openBuildingFacadesModal}
					onClose={() => setOpenBuildingFacadesModal(false)}
				>
					{buildingCurtains.map((curtain, i) => (
						<Fragment key={curtain.id}>
							<CurtainBtn
								id={curtain.id}
								x={curtain.location[0]}
								y={curtain.location[1]}
								dim={curtain.value}
								serviceId={curtain.service_id}
								is_group={curtain.group_id !== null}
							/>
						</Fragment>
					))}
				</BuildingFacades>
			}
		</Page>
	);
}

export default Curtain;