import React, { useEffect, useMemo, useState } from 'react';

// Components
import BuildingFacades from 'src/components/common/modals/BuildingFacades';
import { useDispatch, useSelector } from 'react-redux';
import { Fragment } from 'react';
import RGBBtn from 'src/components/common/buttons/RGBBtn';
import CCTBtn from 'src/components/common/buttons/CCTBtn';
import LightBtn from 'src/components/common/buttons/LightBtn';
import CurtainBtn from 'src/components/common/buttons/CurtainBtn';
import RelayBtn from 'src/components/common/buttons/RelayBtn';
import { useSnackbar } from 'notistack';
import { addMood, setLights } from 'src/redux/actions';
import { alpha, createStyles, makeStyles } from '@material-ui/core';
import AutoGenerateFormModal from 'src/components/common/modals/Form';
import { setDaliBroadcastDim } from 'src/libs/commands';

const useStyles = makeStyles(theme => createStyles({
	addMood: {
		width: '256px',
		height: '170px',
		borderRadius: '4px',
		boxShadow: `0px 3px 3px -2px ${alpha(theme.palette.primary.main, .2)},0px 3px 4px 0px ${alpha(theme.palette.primary.main, .14)},0px 1px 8px 0px ${alpha(theme.palette.primary.main, .12)}`,
		margin: '16px',
		border: `2px dashed ${theme.palette.gray[600]}`,

		'&>button': {
			border: 'none',
			outline: 'none',
			backgroundColor: 'rgba(255, 255, 255, 0.1)',
			cursor: 'pointer',
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			textTransform: 'uppercase',
			fontSize: '16px',
			letterSpacing: '1px',
			color: theme.palette.gray[50]
		}
	}
}));

const BTN = ({ type, children, ...props }) => {
	const typeName = type.split('.');

	if (typeName[0] === 'rgb') return <RGBBtn type={type} {...props} />
	if (typeName[0] === 'cct') return <CCTBtn type={type} {...props} />
	if (type === 'curtain') return <CurtainBtn type={type} {...props} />
	if (type === 'relay') return <RelayBtn type={type} {...props} />

	return <LightBtn type={type} {...props} />
};

const getServices = ({ user, mqtt, services }) => ({ project_id: user?.project_id, mqtt, services });
const Create = ({ building }) => {
	const classes = useStyles();

	const dispatch = useDispatch();

	const { project_id, mqtt, services } = useSelector(getServices);

	const [contextMenuIsOpen, setContextMenuIsOpen] = useState(false);

	const [isOpenFormModal, setIsOpenFormModal] = useState(false);

	const [servicesImplemented, setServicesImplemented] = useState([]);

	const [showServiceOffWarning, setShowServiceOffWarning] = useState(false);

	const { enqueueSnackbar, closeSnackbar } = useSnackbar();

	const filterServicesByBuildingId = useMemo(() => {
		try {
			if (!building) return [];
			return [...services.lights, ...services.relays, ...services.curtains].filter(service => service.building_id === building.id);
		} catch (e) {
			return [];
		}
	}, [services, building]);

	const collectServices = () => servicesImplemented.map(service => ({ service_id: service.id, dim: service.value }));

	const offLight = (light) => {
		const dim = light.value;
		if (light.type === 'rgb') return [dim[0], dim[1], dim[2], dim[3], 0];
		if (light.type === 'cct') return [dim[0], 0];

		return 0;
	};

	/* Add service */
	const addServiceToImplementing = (service) => {
		const changedServices = [...servicesImplemented];
		const serviceIndex = changedServices.findIndex(({ id, type }) => service.id === id && service.type === type);

		if (serviceIndex === -1) changedServices.push(service);
		else changedServices[serviceIndex] = service;

		setServicesImplemented(changedServices);
	};

	/* Add mood */
	const onAddMood = (_e, values) => {
		const changedServices = collectServices();
		dispatch(
			addMood(building.id, {
				name: values.name,
				runtime: values.runtime,
				services: changedServices
			})
		);

		enqueueSnackbar("Mood saved successfully.", {
			key: 'not_saved',
			variant: 'success',
			autoHideDuration: 3000,
			preventDuplicate: true,
			id: 'saved'
		});
	}

	/* Show configuration modal */
	const saveMood = () => {
		closeSnackbar('question');
		setContextMenuIsOpen(false);

		setIsOpenFormModal(true);
	};

	/* Don't save mood */
	const closeModal = (showSnackbar = true) => {
		setContextMenuIsOpen(false);
		closeSnackbar('question');

		if (showSnackbar) enqueueSnackbar("Could not be saved.", {
			key: 'not_saved',
			variant: 'error',
			autoHideDuration: 3000,
			preventDuplicate: true,
			id: 'not_saved'
		});
	};

	/* When the user wants to leave modal */
	const handleCloseModal = () => {
		if (!filterServicesByBuildingId.length || !servicesImplemented.length) {
			closeModal(false);
			return;
		}

		enqueueSnackbar("Do you want to save mood?", {
			action: () => (
				<>
					<button
						type="button"
						onClick={saveMood}
						className="btn text-success btn-sm shadow-none font-size-12"
					>
						Save
					</button>
					<button
						type="button"
						onClick={closeModal}
						className="btn text-danger btn-sm shadow-none font-size-12"
					>
						Cancel
					</button>
				</>
			),
			anchorOrigin: {
				horizontal: 'center',
				vertical: 'bottom',
			},
			key: 'question',
			autoHideDuration: 10000,
			onAbort: closeModal,
			preventDuplicate: true,
			id: 'question'
		});
	};

	const turnOffMaster = (masters, cb, index = 0) => {
		try {
			if (masters.length === 0) return cb();

			mqtt.publish(`${project_id}/${masters[index]}/Dali/In`, setDaliBroadcastDim('OFF', 1));

			if (masters[index + 1]) return setTimeout(() => turnOffMaster(masters, cb, index + 1), 1000);

			setTimeout(cb, 1000);
		} catch (e) {
			setTimeout(cb, 1000);
		}
	}

	useEffect(() => {
		if (!contextMenuIsOpen) return;

		setShowServiceOffWarning(true);

		dispatch(
			setLights(
				filterServicesByBuildingId.map((service) => ({
					...service,
					value: offLight(service)
				}))
			)
		);

		const masters = filterServicesByBuildingId.map(service => service.master_id).filter(Boolean);

		turnOffMaster([...new Set(masters)], () => setShowServiceOffWarning(false));
	}, [contextMenuIsOpen]);

	return (
		<>
			<div className={`${classes.addMood} position-relative overflow-hidden p-0`}>
				<button
					onClick={() => setContextMenuIsOpen(true)}
					type="button"
					className="position-relative w-100 h-100 p-0 m-0"
				>
					Add New Mood
				</button>
			</div>

			<BuildingFacades
				building={building}
				open={contextMenuIsOpen}
				onClose={handleCloseModal}
			>
				{showServiceOffWarning
					?
					<div className='d-flex justify-content-center align-items-center position-absolute w-100 h-100' style={{ top: 0, left: 0, backgroundColor: 'rgba(0, 0, 0, 0.75)' }}>
						Wait until the lights go out ...
					</div>
					: filterServicesByBuildingId.map(service => (
						<Fragment key={service.id}>
							<BTN
								type={service.type}
								id={service.id}
								x={service.location[0]}
								y={service.location[1]}
								dim={service.value}
								serviceId={service.service_id}
								masterId={service.master_id}
								is_group={Boolean(service.group_id)}
								groupId={service.group_id}
								options={service.options}
								onRun={addServiceToImplementing}
							/>
						</Fragment>
					))
				}
			</BuildingFacades>

			<AutoGenerateFormModal
				open={isOpenFormModal}
				onClose={() => setIsOpenFormModal(false)}
				onSubmit={onAddMood}
				inputs={[
					{
						autoFocus: true,
						name: 'name',
						label: 'Name',
						required: true,
						placeholder: 'Enter mood name'
					}
				]}
			/>
		</>
	);
}

export default Create;
