const INITIAL_STATE = {
	authorized: false,

	mqtt: null,

	user: null,

	buildings: [],

	scenes: [],

	moods: [],

	jobs: [],

	services: {
		lights: [],
		curtains: [],
		relays: [],
	},

	thermostats: []
}

export const reducer = (state = INITIAL_STATE, action) => {
	const { type, payload } = action;

	if (type === 'SET_MQTT_CONNECTION') return { ...state, mqtt: payload };

	if (type === 'SET_AUTHORIZED_STATUS') return { ...state, authorized: payload };

	if (type === 'SET_USER_INFORMATION') return { ...state, user: payload };

	if (type === 'FETCH_BUILDINGS') return { ...state, buildings: payload };

	if (type === 'FETCH_SCENES') return { ...state, scenes: payload };

	if (type === 'FETCH_SERVICES') {
		const lights = [];
		const curtains = [];
		const relays = [];

		for (let i = 0; i < payload.length; i++) {
			const service = payload[i];
			service.value = String(service.value).split(',');
			if (service.value.length === 1) service.value = service.value[0];

			if (['dali_light', 'rgb.dt6', 'rgb.dt8', 'cct.dt6', 'cct.dt8'].includes(service.type)) lights.push(service);
			else if (service.type === 'curtain') curtains.push(service);
			else if (service.type === 'relay') relays.push(service);
		}

		return { ...state, services: { lights, curtains, relays } };
	}

	if (type === 'FETCH_THERMOSTATS') return { ...state, thermostats: payload };

	if (type === 'SET_THERMOSTATS') return { ...state, thermostats: payload };

	if (type === 'SET_LIGHT') return { ...state, services: { ...state.services, lights: payload } };

	if (type === 'SET_CURTAIN') return { ...state, services: { ...state.services, curtains: payload } };

	if (type === 'SET_RELAY') return { ...state, services: { ...state.services, relays: payload } };

	if (type === 'FETCH_MOODS') return { ...state, moods: payload };

	if (type === 'FETCH_JOBS') return { ...state, jobs: payload };

	if (type === 'ADD_JOB') return { ...state, jobs: [...state.jobs, payload] };

	if (type === 'DELETE_JOB') return { ...state, jobs: state.jobs.filter(job => job.id !== payload) };

	if (type === 'ADD_MOOD') return {
		...state, moods: [...state.moods, {
			...payload,
			services: JSON.parse(payload.services)
		}]
	};

	if (type === 'CHANGE_MOOD_NAME') {
		const moods = state.moods.map(mood => {
			if (mood.id === payload.id) return { ...mood, name: payload.name };
			return mood;
		});

		return { ...state, moods };
	}

	if (type === 'DELETE_MOOD') return { ...state, moods: state.moods.filter(mood => mood.id !== payload) };

	return state;
};
