import React from 'react';
import { Fade, Modal as BasicModal } from '@material-ui/core';

const Modal = ({ open, children, ...props }) => {
	return (
		<BasicModal
			style={{
				width: '100%',
				height: '100%'
			}}
			open={open}
			{...props}
		>
			<Fade in={open}>
				{children}
			</Fade>
		</BasicModal>
	);
}

export default Modal;
