import { useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { useDispatch } from 'react-redux';
import { setLights, updateDim } from 'src/redux/actions';
import useLongPress from 'src/hooks/useLongPress';

// Components
import { LightModal } from '../modals/Services';

// Icons
import { LightSVG, LightsSVG } from 'src/icons';
import { useSelector } from 'react-redux';
import { setGroupDaliLightDim, setSingleDaliLightDim } from 'src/libs/commands';

const useStyles = makeStyles(theme => createStyles({
	light: {
		width: '48px',
		height: '48px',
		backgroundColor: theme.palette.warning.main,
		boxShadow: `0px -4px 12px ${theme.palette.secondary.main}`,
		transition: theme.transitions.create(['all']),

		'& svg': {
			width: '24px',
			height: '24px',
		},

		[theme.breakpoints.down('md')]: {
			width: '36px',
			height: '36px',
	
			'& svg': {
				width: '20px',
				height: '20px',
			},
		},

		[theme.breakpoints.down('sm')]: {
			width: '28px',
			height: '28px',
	
			'& svg': {
				width: '18px',
				height: '18px',
			},
		},
	},
}));

const getStates = state => ({ lights: state.services.lights, mqtt: state.mqtt, project_id: state.user?.project_id });
const LightBtn = ({ id, is_group, dim, serviceId, groupId, masterId, x, y, type, onRun }) => {
	const classes = useStyles();

	const { lights, mqtt, project_id } = useSelector(getStates);
	const dispatch = useDispatch();

	const [openModal, setOpenModal] = useState(false);

	const publish = (id, brightness) => {
		if (!mqtt || process.env.REACT_APP_MQTT_STATUS !== "ON") return;

		const dim = is_group ? setGroupDaliLightDim(brightness, id) : setSingleDaliLightDim(brightness, id);
		mqtt.publish(`${project_id}/${masterId}/Dali/In`, dim);
	}

	const onChangeBrightness = (brightness) => {
		/* Update API */
		updateDim(id, brightness, type);
		onRun?.({ id, is_group, value: brightness, serviceId, groupId, masterId, x, y });

		/* Update UI */
		const newLights = [...lights].map(light => light.id === id ? { ...light, value: brightness } : light);
		dispatch(setLights(newLights));

		/* Update Device */
		publish(is_group ? groupId : serviceId, brightness);
	}

	const longPressEvent = useLongPress(
		() => setOpenModal(true),
		() => onChangeBrightness(dim < 127 ? 254 : 0),
		{
			delay: 1000
		}
	);

	return (
		<>
			<button
				{...longPressEvent}
				style={{ left: `${x}%`, top: `${y}%`, opacity: ((((dim / 254) * 60) + 40) / 100) }}
				className={`${classes.light} btn text-light position-absolute rounded-circle p-0 m-0`}
				type="button"
			>
				{is_group
					? <LightsSVG />
					: <LightSVG />
				}
			</button>

			{openModal &&
				<LightModal
					onClose={() => setOpenModal(false)}
					onChange={onChangeBrightness}
					open={openModal}
					initialBrightness={+dim}
				/>
			}
		</>
	);
};

export default LightBtn;