import React from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { alpha } from '@material-ui/core';

// Components
import Modal from '../Modal';

const useStyles = makeStyles(theme => createStyles({
	modal: {
		width: '90%',
		height: '90%',
		borderRadius: '4px',
		outline: 'none',
		backgroundColor: theme.palette.primary.main,
		boxShadow: `0px 4px 5px -2px ${alpha(theme.palette.primary.light, .2)},0px 7px 10px 1px ${alpha(theme.palette.primary.light, .14)},0px 2px 16px 1px ${alpha(theme.palette.primary.light, .12)}`,
	},

	facades: {
		boxShadow: `0px 3px 3px -2px ${alpha(theme.palette.primary.light, .2)},0px 3px 4px 0px ${alpha(theme.palette.primary.light, .14)},0px 1px 8px 0px ${alpha(theme.palette.primary.light, .12)}`,

		'& img': {
			maxWidth: '100%',
			height: '100%',
			borderRadius: '4px',
			filter: 'brightness(.8)',
			WebkitFilter: 'brightness(.8)',
		},
	}
}));

const BuildingFacades = ({ open, onClose, building, children }) => {
	const classes = useStyles();

	return (
		<Modal
			open={open}
			onClose={onClose}
		>
			<div className={`${classes.modal} d-flex flex-column justify-content-between position-absolute translate-middle overflow-hidden start-50 top-50 p-0 m-0`}>
				<div className={`${classes.facades} position-relative mw-100 h-100 mx-auto my-0 p-0`}>
					<img alt='building' src={building.image} />
					{children}
				</div>
			</div>
		</Modal>
	);
};

export default BuildingFacades;